import React from "react";
import "./index.scss";
import { Input, Button, Drawer, Modal } from "antd";
import { PlusOutlined, CloseOutlined } from "@ant-design/icons";
import Mytable from "../../component/mytable/index";
import Threelinkage from "../../component/threelinkage/index";
import $ from "../../utils/index";
import {
  query_organization,
  add_organization,
  update_organization,
  _aadegiilnnoortz,
} from "./request";
class Domin extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      handledrawer: false,
      isaddmsg: true,
      organization_name: "",
      currentpage: 1,
      pagesize: 13,
      count: 0,
      mobile: "",
      ename: "",
      city: "",
      domainId: "",
      searchipt: "",
      searchipts: "",
      isdeletecon: false,
      dataname: [
        { prop: "key", label: "#", width: "18%" },
        { prop: "organization_name", label: "组织名称", width: "18%" },
        { prop: "ename", label: "登录域", width: "18%" },
        { prop: "city", label: "地区", width: "22%" },
        { prop: "account", label: "初始账号", width: "18%" },
      ],
      datamsg: [],
    };
  }
  //表格点击编辑
  updata_row = (val) => {
    this.setState({
      handledrawer: true,
      isaddmsg: false,
      organization_name: val.organization_name,
      ename: val.ename,
      domainId: val.domainId,
      city: val.city,
      old_ename: val.ename,
      mobile: val.phone,
      user_id: val.user_id,
    });
  };
  //表格点击删除
  delete_row = (val) => {
    this.setState({
      domainId: val.domainId,
      organization_name: val.organization_name,
      isdeletecon: true,
    });
  };
  //确定删除
  rmconfig = async () => {
    let res = await _aadegiilnnoortz(this, { domainId: this.state.domainId });
    if (res.data.code === 200) {
      $.success(res.data.message);
      this.setState(
        {
          domainId: "",
          organization_name: "",
          isdeletecon: false,
        },
        () => {
          this.getquery_organization();
        }
      );
    } else {
      $.error(res.data.message);
      this.setState(
        {
          domainId: "",
          organization_name: "",
          isdeletecon: false,
        },
        () => {
          this.getquery_organization();
        }
      );
    }
  };
  //查询
  getquery_organization = async () => {
    let res = await query_organization(this, {
      ename: this.state.searchipts,
      currentpage: this.state.currentpage,
      pagesize: this.state.pagesize,
    });
    this.setState({
      count: res.data.data_count,
      datamsg: res.data.content_list
        ? res.data.content_list.map((res, index) => {
            res.key = index + 1;
            return res;
          })
        : [],
    });
  };
  //添加确定按钮
  configdraw = async () => {
    //确定添加
    if (this.state.organization_name.length === 0) {
      $.error("请填写组织名称");
      return;
    }
    if (this.state.ename.length === 0) {
      $.error("请填写登录域");
      return;
    }

    if (!this.state.city.includes("-")) {
      $.error("请选择地区，至少是市级别！");
      return;
    }
    if (this.state.isaddmsg) {
      if (this.state.mobile.length === 0) {
        $.error("请填写手机号");
        return;
      }
      let res = await add_organization(this, {
        organization_name: this.state.organization_name,
        mobile: this.state.mobile,
        ename: this.state.ename,
        city: this.state.city,
      });
      if (res.data.code === 200) {
        $.success("组织添加成功");
        this.setState(
          {
            organization_name: "",
            mobile: "",
            ename: "",
            city: "",
            handledrawer: false,
          },
          () => {
            this.getquery_organization();
          }
        );
      } else {
        if (res.data.message === "地区不可重复") {
          $.error("该地区已存在");
        } else if (res.data.message === "域不可重复") {
          $.error("域名重复，请重新输入");
        } else {
          $.error(res.data.message);
        }
      }
    } else {
      //确定修改
      let res = await update_organization(this, {
        organization_name: this.state.organization_name,
        mobile: this.state.mobile,
        ename: this.state.old_ename,
        city: this.state.city,
        domainId: this.state.domainId,
        new_ename: this.state.ename,
        user_id: this.state.user_id,
      });
      if (res.data.code === 200) {
        $.success("组织修改成功");
        this.setState(
          {
            organization_name: "",
            mobile: "",
            ename: "",
            city: "",
            domainId: "",
            accountID: "",
            handledrawer: false,
          },
          () => {
            this.getquery_organization();
          }
        );
      } else {
        $.error(res.data.message);
      }
    }
  };
  render() {
    let {
      isaddmsg,
      organization_name,
      mobile,
      ename,
      searchipt,
      city,
    } = this.state;
    return (
      <div className="domin_page">
        <Modal
          visible={this.state.isdeletecon}
          footer={null}
          width={330}
          bodyStyle={{ height: 220 }}
          keyboard={true}
          maskClosable={false}
          wrapClassName={"domin_page_deletederlog"}
          onCancel={() => {
            this.setState({
              isdeletecon: false,
            });
          }}
          centered={true}
        >
          <div className="header">温馨提示</div>
          <div className="tip">
            确认删除组织<span>{organization_name}</span> ？
          </div>
          <div className="message">
            <span>*</span>删除后该域下所有账号将不可使用
          </div>
          <div className="footer">
            <Button onClick={this.rmconfig}>确认</Button>
            <Button
              onClick={() => {
                this.setState({
                  isdeletecon: false,
                });
              }}
            >
              取消
            </Button>
          </div>
        </Modal>
        <Drawer
          placement="right"
          closable={false}
          onClose={() => {
            this.setState({
              organization_name: "",
              mobile: "",
              ename: "",
              city: "",
              handledrawer: false,
            });
          }}
          visible={this.state.handledrawer}
          width={500}
        >
          <div className="drawer_container">
            <div className="header">
              {isaddmsg ? "添加组织" : "编辑组织"}
              <CloseOutlined
                style={{ cursor: "pointer" }}
                className="closebtn"
                onClick={() => {
                  this.setState({
                    organization_name: "",
                    mobile: "",
                    ename: "",
                    city: "",
                    handledrawer: false,
                  });
                }}
              />
            </div>
            <div className="drawer_item">
              <div>组织名称</div>
              <div
                onClick={(e) => {
                  if (e.target.tagName === "path") {
                    this.setState({
                      organization_name: "",
                    });
                  }
                }}
              >
                <Input
                  placeholder="请输入组织名称"
                  allowClear={true}
                  value={organization_name}
                  onInput={(e) => {
                    this.setState({
                      organization_name: e.target.value
                        .replace(/[\s]/g, "")
                        .slice(0, 10),
                    });
                  }}
                />
              </div>
            </div>
            <div className="drawer_item">
              <div>登录域</div>
              <div
                onClick={(e) => {
                  if (e.target.tagName === "path") {
                    this.setState({
                      ename: "",
                    });
                  }
                }}
              >
                <Input
                  placeholder="请输入登录域"
                  allowClear={true}
                  value={ename}
                  onInput={(e) => {
                    this.setState({
                      ename: e.target.value
                        .replace(/[^a-zA-z]/g, "")
                        .slice(0, 20),
                    });
                  }}
                />
              </div>
            </div>
            {/* {isaddmsg && ( */}
            <div className="drawer_item">
              <div>管理员手机号</div>
              <div
                onClick={(e) => {
                  if (e.target.tagName === "path") {
                    this.setState({
                      mobile: "",
                    });
                  }
                }}
              >
                <Input
                  placeholder="请输入管理员手机号"
                  allowClear={true}
                  value={mobile}
                  onInput={(e) => {
                    this.setState({
                      mobile: e.target.value
                        .replace(/[^0-9]/g, "")
                        .slice(0, 11),
                    });
                  }}
                />
              </div>
            </div>
            {/* )} */}
            <div className="drawer_item">
              <div>地区</div>
              <div>
                <Threelinkage
                  city={this.state.city}
                  areavalue={(val) => {
                    this.setState({
                      city: val,
                    });
                  }}
                />
              </div>
            </div>
            <div className="buttonbox">
              <Button
                onClick={() => {
                  this.configdraw();
                }}
              >
                确定
              </Button>
              <Button
                onClick={() => {
                  this.setState({
                    organization_name: "",
                    mobile: "",
                    ename: "",
                    city: "",
                    handledrawer: false,
                  });
                }}
              >
                取消
              </Button>
            </div>
          </div>
        </Drawer>
        <div className="tip">组织管理</div>
        <div className="domin_pagesearch">
          <div>
            <div>登录域</div>
            <div
              onClick={(e) => {
                if (e.target.tagName === "path") {
                  this.setState({
                    searchipt: "",
                  });
                }
              }}
            >
              <Input
                placeholder="请输入登录域"
                value={searchipt}
                allowClear={true}
                onInput={(e) => {
                  this.setState({
                    searchipt: e.target.value.replace(/\s/g, ""),
                  });
                }}
              />
            </div>
          </div>
          <div>
            <div style={{ opacity: "0" }}>123</div>
            <div>
              <Button
                onClick={() => {
                  this.setState(
                    {
                      currentpage: 1,
                      searchipts: this.state.searchipt,
                    },
                    () => {
                      this.getquery_organization();
                    }
                  );
                }}
              >
                查询
              </Button>
            </div>
          </div>
          <div>
            <div style={{ opacity: "0" }}>123</div>
            <div>
              <Button
                onClick={() => {
                  this.setState(
                    {
                      searchipt: "",
                      currentpage: 1,
                    },
                    () => {
                      this.getquery_organization();
                    }
                  );
                }}
              >
                重置
              </Button>
            </div>
          </div>
          <div className="add">
            <Button
              onClick={() => {
                this.setState({ handledrawer: true, isaddmsg: true });
              }}
            >
              <PlusOutlined />
              添加
            </Button>
          </div>
        </div>
        <div className="main" ref="massgebox">
          <div ref="tablebox">
            <Mytable
              dataname={this.state.dataname}
              datamsg={this.state.datamsg}
              currentpage={this.state.currentpage}
              pagesize={this.state.pagesize}
              count={this.state.count}
              handlebtn={true}
              name="setting"
              viewtheimage={(val) => {
                this.updata_row(val);
              }}
              aboutthedisposal={(val) => {
                this.delete_row(val);
              }}
              watchpagechange={(val) => {
                this.setState(
                  {
                    currentpage: val,
                  },
                  () => {
                    this.getquery_organization();
                  }
                );
              }}
            ></Mytable>
          </div>
        </div>
      </div>
    );
  }

  settabbox = () => {
    if (this.refs.tablebox) {
      this.refs.tablebox.style.height = document.body.clientHeight - 250 + "px";
      this.setState(
        {
          pagesize: Math.floor(
            (Math.floor(document.body.clientHeight) - 250) / 50
          ),
        },
        () => {
          this.getquery_organization();
        }
      );
    }
  };
  componentDidUpdate() {
    if (this.refs.tablebox) {
      this.refs.tablebox.style.height = document.body.clientHeight - 250 + "px";
    }
  }
  componentDidMount() {
    this.settabbox();
    window.onresize = $.debounce(this.settabbox, 1000);
  }
  componentWillUnmount() {
    window.onresize = null;
  }
}
export default Domin;
