import React, { Component } from "react";
import "./index.scss";
import { CloseOutlined, VerticalAlignBottomOutlined } from "@ant-design/icons";
import { Base } from "../../config/index";
import $ from "../../utils/index";
export default class Informationmask extends Component {
  state = {
    imgsrc: this.props.informaskmsglist[0],
  };
  settabbox = () => {
    let dom = document.querySelector(".imglist");
    let doms = document.querySelector(".imglist_msg");
    if (dom && doms) {
      doms.style.maxHeight = dom.clientHeight - 120 + "px";
    }
  };
  componentDidUpdate() {
    if (this.refs.tablebox) {
      this.refs.tablebox.style.height = document.body.clientHeight - 250 + "px";
    }
  }
  componentDidMount() {
    this.settabbox();
    window.addEventListener("resize", $.debounce(this.settabbox, 500));
  }
  componentWillUnmount() {
    window.removeEventListener("resize", $.debounce(this.settabbox, 500));
  }
  render() {
    let { picturehandlecencle, informaskmsglist } = this.props;
    let { imgsrc } = this.state;
    return (
      <div className="informationmask_page">
        <div
          className="closemask"
          style={{ cursor: "pointer" }}
          onClick={() => {
            picturehandlecencle(false);
          }}
        >
          <CloseOutlined />
        </div>
        <div className="containermsg">
          <div className="imgviewbox">
            {!imgsrc && (
              <div className="imgviewbox_datebox">
                <div
                  style={{
                    margin: "30px auto",
                    textAlign: "center",
                  }}
                >
                  暂无图片
                </div>
              </div>
            )}
            {imgsrc && (
              <div className="imgviewbox_datebox">
                <div className="imgviewbox_date">{imgsrc && imgsrc.r_time}</div>
                <div className="imgmsg">
                  <img src={imgsrc && imgsrc.file_path} alt="" />
                </div>
              </div>
            )}
          </div>
          <div className="imglist">
            <div className="imglist_title">首页截图</div>
            <div className="line"></div>
            <div className="imglist_msg">
              {informaskmsglist.length === 0 && (
                <div
                  style={{
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  暂无图片
                </div>
              )}
              {informaskmsglist.length !== 0 &&
                informaskmsglist.map((item, index) => {
                  return (
                    <div className="imglist_msg_item" key={index}>
                      <div
                        className="downimgicon"
                        onClick={(e) => {
                          this.downimg(e);
                        }}
                      >
                        <VerticalAlignBottomOutlined />
                      </div>
                      <div>{item.r_time}</div>
                      <div
                        onClick={() => {
                          this.setState({
                            imgsrc: item,
                          });
                        }}
                      >
                        <img src={item.file_path} alt="" />
                      </div>
                    </div>
                  );
                })}
            </div>
          </div>
        </div>
      </div>
    );
  }
  downimg = async (evt) => {
    var img = evt.currentTarget.parentNode.lastElementChild.lastElementChild;
    var urls = img.src;
    window.location.href = `${Base}:9810/data/image?file_path=${urls.replace(
      Base,
      ""
    )}`;
  };
}
