import React, { Component } from "react";
import { CloseOutlined } from "@ant-design/icons";
import { Select, Button, Input, Drawer, Checkbox, Modal } from "antd";
import $ from "../../utils/index";
import Swiper from "swiper";
import "swiper/css/swiper.css";
import "./index.scss";
import {
  to_dispose, //去处置
} from "../../pages/webinformation/request";
import {
  picture_mode_detail,
  dispose_info,
  picture_mode_homepage_picture,
} from "./request";
import { Base } from "../../config/index";
const { Option } = Select;
const { TextArea } = Input;
const CheckboxGroup = Checkbox.Group;
export default class Entiredata extends Component {
  state = {
    datamsg: {},
    disposalrecords: [],
    handledrawer: false,
    resultCode: "",
    checkedListdraw: [],
    plainOptionsdraw: [
      "新闻资讯",
      "音视频",
      "直播",
      "购物交易",
      "互动论坛",
      "手机应用商店",
      "在线教育",
      "其他",
    ],
    postscript: "",
    respicturelist: [],
    imgsrc: "",
    openimgmask: false,
    scrolluploadpage: 1,
  };
  render() {
    let { closeentiredata } = this.props;
    let { datamsg, respicturelist } = this.state;
    return (
      <div className="entiredata">
        {this.state.openimgmask && (
          <div className="imgmask">
            <span
              style={{ cursor: "pointer" }}
              onClick={() => {
                this.setState({
                  imgsrc: "",
                  openimgmask: false,
                });
              }}
            >
              <CloseOutlined />
            </span>
            <div>
              <div>{this.state.imgsrc.r_time}</div>
              <div>
                <img src={this.state.imgsrc.file_path} alt="" />
              </div>
            </div>
          </div>
        )}
        <Drawer
          placement="right"
          closable={false}
          onClose={this.onClose}
          visible={this.state.handledrawer}
          width={500}
        >
          <div className="drawer_container">
            <div className="header">
              去处置
              <CloseOutlined
                onClick={() => {
                  this.onClose();
                }}
                style={{
                  marginLeft: "375px",
                  cursor: "pointer",
                }}
              />
            </div>
            <div>
              <div className="formmsgnew">
                <div className="formmsg_msg">
                  <div>处理结果</div>
                  <div className="formmsg_msg_input">
                    <Select
                      style={{ width: 250 }}
                      value={this.state.resultCode}
                      onChange={(e) => {
                        this.setState({ resultCode: e });
                      }}
                      placeholder="请选择"
                    >
                      <Option value="色情网站">色情网站</Option>
                      <Option value="赌博网站">赌博网站</Option>
                      <Option value="色情和赌博网站">色情和赌博网站</Option>
                      <Option value="政治有害">政治有害</Option>
                      <Option value="危险和涉欺诈">危险和涉欺诈</Option>
                      <Option value="备案信息不符">备案信息不符</Option>
                      <Option value="跳转非法网站">跳转非法网站</Option>
                      <Option value="其他">其他</Option>
                      <Option value="正常网站">正常网站</Option>
                    </Select>
                  </div>
                </div>
                <div className="formmsg_msg">
                  <div>
                    <CheckboxGroup
                      options={this.state.plainOptionsdraw}
                      value={this.state.checkedListdraw}
                      onChange={this.onChangedraw}
                    />
                  </div>
                </div>
                <div className="formmsg_msg">
                  <div>备注</div>
                  <div>
                    <TextArea
                      placeholder="请输入备注信息..."
                      rows={6}
                      value={this.state.postscript}
                      onInput={(e) => {
                        if (e.target.value.length <= 300) {
                          this.setState({
                            postscript: e.target.value,
                          });
                        }
                      }}
                    />
                  </div>
                  <div className="wordcont">
                    {this.state.postscript.length}/300
                  </div>
                </div>
                <div className="formmsg_msg">
                  <div className="formmsg_msgnewbutton">
                    <div className="toleadbox">
                      <Button className="tolead" onClick={this.configdispose}>
                        确定
                      </Button>
                    </div>
                    <div className="tooutbox">
                      <Button
                        className="toout"
                        onClick={() => {
                          this.setState({
                            resultCode: "",
                            checkedListdraw: [],
                            postscript: "",
                          });
                        }}
                      >
                        取消
                      </Button>
                    </div>
                  </div>
                </div>
                {/* 表格 */}
              </div>
            </div>
          </div>
        </Drawer>
        <div className="entiredata_contaier">
          <div>
            <span>
              {this.props.smsverificationlicenseKey &&
                this.props.smsverificationlicenseKey}
            </span>
            <CloseOutlined
              style={{ cursor: "pointer" }}
              onClick={() => {
                closeentiredata(false);
              }}
            />
          </div>
          <div className="entiredata_contaier_msg">
            <div className="fcompletion">网站信息</div>
            <div className="fcompletion_min">
              <div className="hovelisttitle">
                <span>网站名称：</span>
                <span>
                  {datamsg && datamsg.siteName ? datamsg.siteName : "--"}
                </span>
              </div>
              <div className="hovelisttitle">
                <span>顶级域名：</span>
                <a
                  href={
                    "http://www." +
                    (datamsg && datamsg.topDomain ? datamsg.topDomain : "--")
                  }
                  style={{ color: "#3ECACB" }}
                  target="_Blank"
                >
                  {datamsg && datamsg.topDomain ? datamsg.topDomain : "--"}
                </a>
              </div>
              <div className="hovelisttitle">
                <span>网站类型：</span>
                <span>
                  {datamsg && datamsg.website_type
                    ? datamsg.website_type
                    : "--"}
                </span>
              </div>
              <div className="hovelisttitle">
                <span>系统判定：</span>
                {datamsg && datamsg.system_decide ? (
                  <span>
                    {datamsg.system_decide === "正常网站" && (
                      <span style={{ color: "#45A720" }}>正常网站</span>
                    )}
                    {datamsg.system_decide === "风险网站" && (
                      <span style={{ color: "#CB543E" }}>风险网站</span>
                    )}
                    {datamsg.system_decide === "未判定" && (
                      <span style={{ color: "#B3B3B7" }}>未判定</span>
                    )}
                    {datamsg.system_decide !== "风险网站" &&
                      datamsg.system_decide !== "正常网站" &&
                      datamsg.system_decide !== "未判定" && (
                        <span style={{ color: "#CB543E" }}>
                          {datamsg.system_decide}
                        </span>
                      )}
                  </span>
                ) : (
                  "--"
                )}
              </div>
              <div className="hovelisttitle">
                <span>处置结果：</span>
                {datamsg && datamsg.handle_result ? (
                  <span>
                    {datamsg.handle_result === "未处置" && (
                      <span style={{ color: "#B3B3B7" }}>未处置</span>
                    )}
                    {datamsg.handle_result === "正常网站" && (
                      <span style={{ color: "#45A720" }}>正常网站</span>
                    )}
                    {datamsg.handle_result === "风险网站" && (
                      <span style={{ color: "#CB543E" }}>风险网站</span>
                    )}
                    {datamsg.handle_result !== "风险网站" &&
                      datamsg.handle_result !== "未处置" &&
                      datamsg.handle_result !== "正常网站" && (
                        <span style={{ color: "#CB543E" }}>
                          {datamsg.handle_result}
                        </span>
                      )}
                  </span>
                ) : (
                  "--"
                )}
              </div>
              <div className="hovelisttitle">
                <span>地区：</span>
                <span>
                  {datamsg && datamsg.belongingCity
                    ? datamsg.belongingCity
                    : "--"}
                </span>
              </div>
              <div className="hovelisttitle">
                <span>网站负责人：</span>
                <span>
                  {datamsg && datamsg.webmaster ? datamsg.webmaster : "--"}
                </span>
              </div>
              <div className="hovelisttitle">
                <span>联系方式：</span>
                <span>
                  {datamsg && datamsg.webmaster_phone
                    ? datamsg.webmaster_phone
                    : "--"}
                </span>
              </div>
              <div className="hovelisttitle">
                <span>入库时间：</span>
                <span>{datamsg && datamsg.r_time ? datamsg.r_time : "--"}</span>
              </div>
            </div>
          </div>
          <div className="entiredata_contaier_msg">
            <div className="fcompletion">单位信息</div>
            <div className="fcompletion_min">
              <div className="hovelisttitle">
                <span>单位名称：</span>
                <span>
                  {datamsg && datamsg.organizer_unit_name
                    ? datamsg.organizer_unit_name
                    : "--"}
                </span>
              </div>
              <div className="hovelisttitle">
                <span>单位性质：</span>
                <span>
                  {datamsg && datamsg.unitType ? datamsg.unitType : "--"}
                </span>
              </div>
              <div className="hovelisttitle">
                <span>单位详细地址：</span>
                <span>
                  {datamsg && datamsg.organizer_unit_addr
                    ? datamsg.organizer_unit_addr
                    : "--"}
                </span>
              </div>
              <div className="hovelisttitle">
                <span>主体负责人：</span>
                <span>
                  {datamsg && datamsg.principal_person
                    ? datamsg.principal_person
                    : "--"}
                </span>
              </div>
              <div className="hovelisttitle">
                <span>联系方式：</span>
                <span>
                  {datamsg && datamsg.principal_phone
                    ? datamsg.principal_phone
                    : "--"}
                </span>
              </div>
            </div>
          </div>
          <div className="entiredata_contaier_msg">
            <div className="fcompletion">首页截图</div>
            <div className="fcompletion_min_jietu">
              <div className="swiper-container">
                <div className="swiper-wrapper">
                  {respicturelist.length === 0 && (
                    <div
                      style={{
                        width: "100%",
                        height: "150px",
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        fontSize: "30px",
                      }}
                    >
                      暂无图片
                    </div>
                  )}
                  {respicturelist.length !== 0 &&
                    respicturelist.map((item, index) => {
                      return (
                        <div
                          style={{ cursor: "pointer" }}
                          className="swiper-slide"
                          key={index}
                          onClick={() => {
                            this.setState({
                              imgsrc: item,
                              openimgmask: true,
                            });
                          }}
                        >
                          <div> {item.r_time}</div>
                          <img src={item.file_path} alt="" />
                        </div>
                      );
                    })}
                </div>
                <div
                  className="swiper-button-prev"
                  style={{ cursor: "pointer" }}
                ></div>
                <div
                  className="swiper-button-next"
                  style={{ cursor: "pointer" }}
                ></div>
              </div>
            </div>
          </div>
          <div className="entiredata_contaier_msg">
            <div className="gorecordsbox">
              <div className="fcompletion">处理记录</div>
              <div
                className="gorecords"
                style={{ cursor: "pointer" }}
                onClick={() => {
                  this.setState({
                    resultCode: this.state.disposalrecords[0]
                      ? this.state.disposalrecords[0].dispose_result
                      : "请选择",
                    checkedListdraw: this.state.disposalrecords[0]
                      ? this.state.disposalrecords[0].website_type.split(",")
                      : [],
                    postscript: this.state.disposalrecords[0]
                      ? this.state.disposalrecords[0].remark
                      : "",
                    handledrawer: true,
                  });
                }}
              >
                去处置
              </div>
            </div>

            <div
              className="gorecordsboxlist"
              style={{ height: "150px", overflow: "auto" }}
              onScroll={(e) => {
                if (
                  e.currentTarget.scrollHeight - e.currentTarget.scrollTop ===
                    e.currentTarget.clientHeight &&
                  this.state.disposalrecords.length < this.state.listcount
                ) {
                  this.setState(
                    {
                      scrolluploadpage: (this.state.scrolluploadpage += 1),
                    },
                    async () => {
                      let res = await dispose_info(this, {
                        record_detail_id: this.props.record_detail_id,
                        currentPage: this.state.scrolluploadpage,
                        limitCount: 10,
                      });
                      this.setState({
                        disposalrecords: this.state.disposalrecords.concat(
                          res.data.content_list.map((item, index) => {
                            return item;
                          })
                        ),
                      });
                    }
                  );
                }
              }}
            >
              {this.state.disposalrecords &&
                this.state.disposalrecords.map((item, index) => {
                  return (
                    <div className="disposalrecords" key={index}>
                      <div className="hovelisttitle">
                        <span>处置结果：</span>
                        <span style={{ color: "#CB543E" }}>
                          {item.dispose_result}
                        </span>
                      </div>
                      <div
                        className="hovelisttitle"
                        style={{ display: "flex" }}
                      >
                        <span>网站类型：</span>
                        <span className="hoverbox">
                          {item.website_type.split(",").map((res, index) => {
                            return (
                              <div
                                key={index}
                                onMouseEnter={(e) => {
                                  e.currentTarget.children[1].style.width =
                                    res.length * 16 + "px";
                                }}
                                onMouseLeave={(e) => {
                                  e.currentTarget.children[1].style.width =
                                    "0px";
                                }}
                              >
                                {res === "新闻资讯" && (
                                  <span className="iconfont"> &#xe8a7;</span>
                                )}
                                {res === "音视频" && (
                                  <span className="iconfont"> &#xe7da;</span>
                                )}
                                {res === "直播" && (
                                  <span className="iconfont">&#xe78b;</span>
                                )}
                                {res === "购物交易" && (
                                  <span className="iconfont"> &#xe7cf;</span>
                                )}
                                {res === "互动论坛" && (
                                  <span className="iconfont">&#xe8e8;</span>
                                )}
                                {res === "手机应用商店" && (
                                  <span className="iconfont">&#xe7b2;</span>
                                )}
                                {res === "在线教育" && (
                                  <span className="iconfont"> &#xe60d;</span>
                                )}
                                {res === "其他" && (
                                  <span className="iconfont"> &#xe781;</span>
                                )}
                                <span>
                                  <b>{res}</b>
                                </span>
                              </div>
                            );
                          })}
                        </span>
                      </div>
                      <div className="hovelisttitle">
                        <span>处置人：</span>
                        <span>{item.dispose_people}</span>
                      </div>
                      <div className="hovelisttitle">
                        <span>处置时间：</span>
                        <span>{item.r_time && item.r_time.slice(5)}</span>
                      </div>
                      <div className="hovelisttitle">
                        <span>备注：</span>
                        <span>{item.remark}</span>
                      </div>
                    </div>
                  );
                })}
            </div>
          </div>
        </div>
      </div>
    );
  }
  getmsg = async () => {
    let res = await picture_mode_detail(this, {
      record_detail_id: this.props.record_detail_id,
    });
    let ress = await dispose_info(this, {
      record_detail_id: this.props.record_detail_id,
      currentPage: this.state.scrolluploadpage,
      limitCount: 10,
    });
    let respicture = await picture_mode_homepage_picture(this, {
      record_detail_id: this.props.record_detail_id,
    });
    this.setState({
      listcount: ress.data.data_count ? ress.data.data_count : 0,
      datamsg: res.data.content_list,
      disposalrecords: ress.data.content_list,
      respicturelist:
        respicture.data.detailListInfo &&
        respicture.data.detailListInfo.filter((item) => {
          if (item.file_path) {
            item.file_path = `${Base}` + item.file_path;
            return item;
          }
        }),
    });
  };
  componentDidMount() {
    new Swiper(".swiper-container", {
      slidesPerView: 5,
      navigation: {
        //左右分页
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
    });
    this.getmsg();
    let dom = document.querySelector(".entiredata_contaier");
    if (dom) {
      dom.onmouseover = (e) => {
        if (e.target.className === "hovelisttitle") {
          let showBtn = e.target.clientWidth < e.target.scrollWidth;
          if (showBtn) {
            e.target.title = e.target.children[1].innerHTML;
          }
          window.addEventListener("resize", () => {
            let showTest =
              e.target.parentNodeclientWidth < e.target.parentNodescrollWidth;
            if (showTest) {
              e.target.title = e.target.innerHTML;
            }
          });
        }
      };
    }
  }
  componentDidUpdate() {
    new Swiper(".swiper-container", {
      slidesPerView: 5,
      navigation: {
        //左右分页
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
    });
  }
  //抽屉确定
  configdispose = async () => {
    if (this.state.resultCode === "请选择") {
      $.error("请选择处理结果");
      return;
    }
    if (this.state.checkedListdraw.join(",").length === 0) {
      $.error("请选择网站类型");
      return;
    }
    let res = await to_dispose(this, {
      dispose_result: this.state.resultCode,
      site_type: this.state.checkedListdraw.join(","),
      remark: this.state.postscript,
      uid: localStorage.getItem("uid"),
      record_detail_id: this.props.record_detail_id,
    });
    if (res.data.status === 200) {
      $.success("处置成功");
      this.onClose();
      this.getmsg();
    } else {
      $.error("处置失败");
    }
  };
  //抽屉的多选
  onChangedraw = (checkedListdraw) => {
    this.setState({
      checkedListdraw,
    });
  };
  onClose = () => {
    this.setState({
      handledrawer: false,
    });
  };
}
