import React from "react";
import "./index.scss";
import { Input, Button, Drawer, Modal, Select } from "antd";
import { PlusOutlined, CloseOutlined } from "@ant-design/icons";
import Mytable from "../../component/mytable/index";
import $ from "../../utils/index";
import { query_user, add_user, update_user, del_user } from "./request";
import { query_role } from "../handelrole/request";
const { Option } = Select;
class User extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isaddmsg: false,
      handledrawer: false,
      isdeletecon: false,
      currentpage: 1,
      pagesize: 13,
      count: 0,
      username: "",
      name: "",
      password: "",
      mobile: "",
      selectval: "",
      searchipt: "",
      searchipts: "",
      dataname: [
        { prop: "key", label: "#", width: "14.6%" },
        { prop: "name", label: "用户名称", width: "14.6%" },
        { prop: "username", label: "账号", width: "14.6%" },
        { prop: "password", label: "密码", width: "14.6%" },
        { prop: "mobile", label: "手机号", width: "14.6%" },
        {
          prop: "create_times",
          label: "创建时间",
          width: "14.6%",
        },
      ],
      datamsg: [],
      listcounts: 0,
      scrolluploadpage: 1,
    };
  }
  //表格点击编辑
  updata_row = (val) => {
    this.setState({
      isaddmsg: false,
      handledrawer: true,
      name: val.name,
      username: val.username,
      user_id: val.user_id,
      role_id: val.role_id,
      mobile: val.mobile,
      selectval: val.role_name,
      // password: val.password,
    });
  };
  //表格点击删除
  delete_row = (val) => {
    this.setState({
      name: val.name,
      user_id: val.user_id,
      isdeletecon: true,
    });
  };
  rmconfig = async () => {
    if (+this.state.user_id === +localStorage.getItem("uid")) {
      $.error("用户不能删除自己");
      return;
    }
    let res = await del_user(this, {
      ename: localStorage.getItem("ename"),
      user_id: this.state.user_id,
    });
    if (res.data.code === 200) {
      $.success("删除成功");
      if (this.state.user_id === +localStorage.getItem("uid")) {
        this.props.history.push("/login");
      }
      this.setState(
        {
          username: "",
          name: "",
          password: "",
          mobile: "",
          isdeletecon: false,
        },
        () => {
          this.getquery_user();
        }
      );
    } else {
      $.error(res.data.message);
      this.setState(
        {
          username: "",
          name: "",
          password: "",
          mobile: "",
          isdeletecon: false,
        },
        () => {
          this.getquery_user();
        }
      );
    }
  };
  //添加确定按钮
  configdraw = async () => {
    let resID = await this.getquery_roleID(this.state.selectval);

    if (this.state.name.length === 0) {
      $.error("请输入用户名");
      return;
    }
    if (this.state.username.length === 0) {
      $.error("请输入账号");
      return;
    }
    if (this.state.selectval.length === 0) {
      $.error("请选择角色");
      return;
    }
    if (this.state.mobile.length === 0) {
      $.error("请输入手机号");
      return;
    }
    //确定添加
    if (this.state.isaddmsg) {
      if (this.state.password.length === 0) {
        $.error("请输入密码");
        // 密码格式不正确;
        return;
      }
      let res = await add_user(this, {
        ename: localStorage.getItem("ename"),
        name: this.state.name,
        username: this.state.username,
        password: this.state.password,
        role_id: resID,
        mobile: this.state.mobile,
        uid: localStorage.getItem("uid"),
      });
      if (res.data.code === 200) {
        $.success("用户添加成功");
        this.setState(
          {
            ename: "",
            name: "",
            username: "",
            password: "",
            role_id: "",
            mobile: "",
            uid: "",
            handledrawer: false,
          },
          () => {
            this.getquery_user();
          }
        );
      } else {
        $.error(res.data.message);
      }
    } else {
      //确定修改
      let res = await update_user(this, {
        ename: localStorage.getItem("ename"),
        name: this.state.name,
        username: this.state.username,
        password: this.state.password,
        role_id: resID,
        mobile: this.state.mobile,
        user_id: this.state.user_id,
      });
      if (res.data.code === 200) {
        $.success("用户修改成功");
        this.setState(
          {
            ename: "",
            name: "",
            username: "",
            password: "",
            role_id: "",
            mobile: "",
            uid: "",
            handledrawer: false,
          },
          () => {
            this.getquery_user();
          }
        );
      } else {
        $.error(res.data.message);
      }
    }
  };
  //查询
  getquery_user = async () => {
    let res = await query_user(this, {
      ename: localStorage.getItem("ename"),
      name: this.state.searchipts,
      currentpage: this.state.currentpage,
      pagesize: this.state.pagesize,
    });
    // field  menu;
    if (res.data.code === 200) {
      this.setState({
        count: res.data.data_count,
        datamsg: res.data.content_list.map((res, index) => {
          res.create_times = res.create_time;
          res.key = index + 1;
          return res;
        }),
      });
    }
  };
  //获取角色列表
  getquery_role = async () => {
    let res = await query_role(this, {
      ename: localStorage.getItem("ename"),
      currentpage: this.state.scrolluploadpage,
      limitCount: 10,
    });
    if (res.data.code === 200) {
      this.setState({
        listcounts: res.data.data_count,
        rolelist: res.data.content_list,
        selectval: res.data.content_list[0].role_name,
      });
    }
  };
  //获取角色ID
  getquery_roleID = async (role_name) => {
    let res = await query_role(this, {
      ename: localStorage.getItem("ename"),
      currentpage: 1,
      limitCount: 10,
      role_name: role_name,
    });
    if (res.data.code === 200) {
      return res.data.content_list[0].role_id;
    }
  };
  render() {
    let {
      isaddmsg,
      username,
      name,
      password,
      mobile,
      rolelist,
      selectval,
      searchipt,
    } = this.state;
    return (
      <div className="user_page">
        <Modal
          visible={this.state.isdeletecon}
          footer={null}
          width={330}
          bodyStyle={{ height: 220 }}
          keyboard={true}
          maskClosable={false}
          wrapClassName={"domin_page_deletederlog"}
          onCancel={() => {
            this.setState({
              username: "",
              name: "",
              password: "",
              mobile: "",
              isdeletecon: false,
            });
          }}
          centered={true}
        >
          <div className="header">温馨提示</div>
          <div className="tip">
            确认删除用户<span>{name}</span> ？
          </div>
          <div className="footer">
            <Button onClick={this.rmconfig}>确认</Button>
            <Button
              onClick={() => {
                this.setState({
                  username: "",
                  name: "",
                  password: "",
                  mobile: "",
                  isdeletecon: false,
                });
              }}
            >
              取消
            </Button>
          </div>
        </Modal>
        <Drawer
          placement="right"
          closable={false}
          onClose={() => {
            this.setState({
              username: "",
              name: "",
              password: "",
              mobile: "",
              handledrawer: false,
            });
          }}
          visible={this.state.handledrawer}
          width={500}
        >
          <div className="drawer_container">
            <div className="header">
              {isaddmsg ? "添加用户" : "编辑用户"}
              <CloseOutlined
                style={{ cursor: "pointer" }}
                className="closebtn"
                onClick={() => {
                  this.setState({
                    username: "",
                    name: "",
                    password: "",
                    mobile: "",
                    handledrawer: false,
                  });
                }}
              />
            </div>
            <div className="drawer_item">
              <div>用户名</div>
              <div
                onClick={(e) => {
                  if (e.target.tagName === "path") {
                    this.setState({
                      name: "",
                    });
                  }
                }}
              >
                <Input
                  placeholder="请输入用户名"
                  allowClear={true}
                  value={name}
                  onInput={(e) => {
                    this.setState({
                      name: e.target.value
                        .replace(
                          /[！@#￥%……&*（）——+{}“：《》？!@#$%^&*()_+">< ]/g,
                          ""
                        )
                        .slice(0, 10),
                    });
                  }}
                />
              </div>
            </div>
            <div className="drawer_item">
              <div>账号</div>
              <div
                onClick={(e) => {
                  if (e.target.tagName === "path") {
                    this.setState({
                      username: "",
                    });
                  }
                }}
              >
                <Input
                  placeholder="请输入账号"
                  allowClear={true}
                  value={username}
                  disabled={!this.state.isaddmsg}
                  onInput={(e) => {
                    this.setState({
                      username: e.target.value
                        .replace(/[^a-zA-Z]/g, "")
                        .slice(0, 10),
                    });
                  }}
                />
              </div>
            </div>
            <div className="drawer_item">
              <div>密码</div>
              <div
                onClick={(e) => {
                  if (e.target.tagName === "path") {
                    this.setState({
                      password: "",
                    });
                  }
                }}
              >
                <Input
                  placeholder="请输入密码"
                  allowClear={true}
                  value={password}
                  onInput={(e) => {
                    this.setState({
                      password: e.target.value
                        .replace(
                          /[^0-9A-Za-z!@#$%^&*<>?:"/.,;'！@#￥%……&*？，。、‘；]/g,
                          ""
                        )
                        .slice(0, 20),
                    });
                  }}
                />
              </div>
            </div>
            <div className="drawer_item">
              <div>角色</div>
              <div>
                <Select
                  value={selectval}
                  defaultValue={selectval}
                  style={{ width: 120 }}
                  onPopupScroll={async (e) => {
                    if (
                      e.currentTarget.scrollHeight -
                        e.currentTarget.scrollTop ===
                        e.currentTarget.clientHeight &&
                      this.state.rolelist.length < this.state.listcounts
                    ) {
                      let res = await query_role(this, {
                        ename: localStorage.getItem("ename"),
                        currentpage: (this.state.scrolluploadpage += 1),
                        limitCount: 10,
                      });
                      if (res.data.code === 200) {
                        this.setState({
                          scrolluploadpage: (this.state.scrolluploadpage += 1),
                          rolelist: this.state.rolelist.concat(
                            res.data.content_list
                          ),
                        });
                      }
                    }
                  }}
                  onChange={(e) => {
                    this.setState({
                      selectval: e,
                    });
                  }}
                >
                  {rolelist &&
                    rolelist.map((res) => {
                      return (
                        <Option value={res.role_name} key={res.role_name}>
                          {res.role_name}
                        </Option>
                      );
                    })}
                </Select>
              </div>
            </div>
            <div className="drawer_item">
              <div>绑定手机号</div>
              <div
                onClick={(e) => {
                  if (e.target.tagName === "path") {
                    this.setState({
                      mobile: "",
                    });
                  }
                }}
              >
                <Input
                  placeholder="请输入手机号"
                  allowClear={true}
                  value={mobile}
                  onInput={(e) => {
                    this.setState({
                      mobile: e.target.value
                        .replace(/[^0-9]/g, "")
                        .slice(0, 11),
                    });
                  }}
                />
              </div>
            </div>
            <div className="buttonbox">
              <Button
                onClick={() => {
                  this.configdraw();
                }}
              >
                确定
              </Button>
              <Button
                onClick={() => {
                  this.setState({
                    username: "",
                    name: "",
                    password: "",
                    mobile: "",
                    handledrawer: false,
                  });
                }}
              >
                取消
              </Button>
            </div>
          </div>
        </Drawer>
        <div className="tip">用户管理</div>
        <div className="user_pagesearch">
          <div>
            <div>用户名称</div>
            <div
              onClick={(e) => {
                if (e.target.tagName === "path") {
                  this.setState({
                    searchipt: "",
                  });
                }
              }}
            >
              <Input
                placeholder="请输入用户名称"
                value={searchipt}
                allowClear={true}
                onInput={(e) => {
                  this.setState({
                    searchipt: e.target.value.replace(/\s/g, ""),
                  });
                }}
              />
            </div>
          </div>
          <div>
            <div style={{ opacity: "0" }}>123</div>
            <div>
              <Button
                onClick={() => {
                  this.setState(
                    {
                      currentpage: 1,
                      searchipts: this.state.searchipt,
                    },
                    () => {
                      this.getquery_user();
                    }
                  );
                }}
              >
                查询
              </Button>
            </div>
          </div>
          <div>
            <div style={{ opacity: "0" }}>123</div>
            <div>
              <Button
                onClick={() => {
                  this.setState(
                    {
                      searchipt: "",
                      currentpage: 1,
                    },
                    () => {
                      this.getquery_user();
                    }
                  );
                }}
              >
                重置
              </Button>
            </div>
          </div>
          <div className="add">
            <Button
              onClick={() => {
                this.setState({
                  handledrawer: true,
                  isaddmsg: true,
                  selectval: "管理员",
                });
              }}
            >
              <PlusOutlined />
              添加
            </Button>
          </div>
        </div>
        <div className="main" ref="massgebox">
          <div ref="tablebox">
            <Mytable
              dataname={this.state.dataname}
              datamsg={this.state.datamsg}
              handlebtn={true}
              name="setting"
              currentpage={this.state.currentpage}
              pagesize={this.state.pagesize}
              count={this.state.count}
              watchpagechange={(val) => {
                this.setState(
                  {
                    currentpage: val,
                  },
                  () => {
                    this.getquery_user();
                  }
                );
              }}
              viewtheimage={(val) => {
                this.updata_row(val);
              }}
              aboutthedisposal={(val) => {
                this.delete_row(val);
              }}
            ></Mytable>
          </div>
        </div>
      </div>
    );
  }
  settabbox = () => {
    if (this.refs.tablebox) {
      this.refs.tablebox.style.height = document.body.clientHeight - 250 + "px";
      this.setState(
        {
          pagesize: Math.floor(
            (Math.floor(document.body.clientHeight) - 250) / 50
          ),
        },
        () => {
          this.getquery_user();
        }
      );
    }
  };
  componentDidUpdate() {
    if (this.refs.tablebox) {
      this.refs.tablebox.style.height = document.body.clientHeight - 250 + "px";
    }
  }
  componentDidMount() {
    this.getquery_role();
    this.settabbox();
    window.onresize = $.debounce(this.settabbox, 1000);
  }
  componentWillUnmount() {
    window.onresize = null;
  }
}
export default User;
