import Login from "../pages/login/index";
import Layout from "../pages/layout/index";
import Handeldomin from "../pages/handeldomin/index";
import Handelrole from "../pages/handelrole/index";
import Handeluser from "../pages/handeluser/index";
import Urlscreenshots from "../pages/urlscreenshots/index";
import Viewlog from "../pages/viewlog/index";
import webinformation from "../pages/webinformation/index";
export default [
  {
    path: "/login",
    name: "Login",
    component: Login,
  },
  {
    path: "/index",
    name: "Layout",
    component: Layout,
    children: [
      {
        path: "/index/handeldomin",
        name: "Handeldomin",
        cdoe: {},
        component: Handeldomin,
      },
      {
        path: "/index/handelrole",
        name: "Handelrole",
        cdoe: "40004",
        component: Handelrole,
      },
      {
        path: "/index/handeluser",
        name: "Handeluser",
        cdoe: "40004",
        component: Handeluser,
      },
      {
        path: "/index/handeldomin",
        name: "Handeldomin",
        cdoe: "40004",
        component: Handeldomin,
      },
      {
        path: "/index/urlscreenshots",
        name: "Urlscreenshots",
        cdoe: "40002",
        component: Urlscreenshots,
      },
      {
        path: "/index/viewlog",
        name: "Viewlog",
        cdoe: "40003",
        component: Viewlog,
      },
      {
        path: "/index/webinformation",
        name: "webinformation",
        cdoe: "40001",
        component: webinformation,
      },
    ],
  },
  {
    from: "/",
    to: "/login",
  },
];
