import React from "react";
import "./index.scss";
import { Button, Input } from "antd";
import { LeftOutlined } from "@ant-design/icons";
import $ from "../../utils/index";
import { loginregion, smsverification, userloginapi } from "./request";
//redux
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import Action from "../../store/action";
import routes from "../../router/routerConfig"; //路由表
class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isuserlogin: false,
      issendvarifiation: true,
      region: "",
      accountnumber: "",
      password: "",
      varifiactoncode: "",
      numtime: 60,
      isclicksend: false,
    };
  }
  componentDidMount() {
    if (localStorage.getItem("ename")) {
      this.setState(
        {
          region: localStorage.getItem("ename"),
        },
        () => {
          this.touserlogin();
        }
      );
    }
    this.handleresize();
    window.addEventListener("resize", this.setwidthheigh, true);

    document.body.onkeyup = (e) => {
      if (this.props.history.location.pathname === "/login") {
        if (e.keyCode === 13 && this.state.isuserlogin) {
          this.userlogin();
        } else if (e.keyCode === 13 && !this.state.isuserlogin) {
          this.touserlogin();
        }
      }
    };
  }
  setwidthheigh = () => {
    this.refs.logo_box.style.height =
      this.refs.logo_box.clientWidth * 0.176 +
      this.refs.logo_box.clientWidth +
      "px";
  };
  componentWillUnmount() {
    window.removeEventListener("resize", this.setwidthheigh, true);
    clearInterval(this.timer);
  }
  componentDidUpdate() {
    if (this.state.numtime === 0) {
      clearInterval(this.timer);
      this.setState({
        issendvarifiation: true,
        numtime: 60,
      });
    }
  }
  render() {
    let {
      isuserlogin,
      region,
      accountnumber,
      password,
      varifiactoncode,
      issendvarifiation,
      numtime,
    } = this.state;
    let loginbox = null;
    if (isuserlogin) {
      loginbox = (
        <>
          <div className="usernaem">
            <Input
              placeholder="请输入手机号"
              value={accountnumber}
              onChange={(e) => {
                this.setState({
                  accountnumber: e.target.value
                    .replace(/[^0-9]/g, "")
                    .slice(0, 11),
                });
              }}
            />
          </div>
          <div className="password">
            <Input
              placeholder="请输入密码"
              value={password}
              type="password"
              onChange={(e) => {
                this.setState({ password: e.target.value.replace(/\s/g, "") });
              }}
            />
          </div>
          <div className="authcode">
            <Input
              placeholder="请输入短信验证码"
              //   disabled={issendvarifiation}
              value={varifiactoncode}
              onChange={(e) => {
                this.setState({
                  varifiactoncode: e.target.value
                    .replace(/\D/g, "")
                    .slice(0, 6),
                });
              }}
            />
            {issendvarifiation ? (
              <Button
                className="loginbtn"
                onClick={() => {
                  this.sendvarifiactoncode();
                }}
              >
                {!this.state.isclicksend && "发送验证码"}
                {this.state.isclicksend && "重新发送"}
              </Button>
            ) : (
              <Button
                className="loginbtn"
                disabled={!issendvarifiation}
                style={{
                  background: "#ccc",
                  borderColor: "#ccc",
                }}
              >
                <span style={{ marginLeft: "-3px" }}>{numtime}秒重新发送</span>
              </Button>
            )}
          </div>
          <div className="configlogin">
            <Button
              className="loginbtn"
              onClick={() => {
                this.userlogin();
              }}
            >
              登录
            </Button>
          </div>
          <div
            className="backbtn"
            onClick={() => {
              this.backdomine();
            }}
          >
            <LeftOutlined />
          </div>
        </>
      );
    } else {
      loginbox = (
        <>
          <div className="dominname">
            <Input
              placeholder="请输入域名"
              value={region}
              onChange={(e) => {
                this.setState({
                  region: e.target.value.replace(/[^a-zA-z]/g, ""),
                });
              }}
            />
          </div>
          <div className="configlogin" style={{ marginTop: "-20%" }}>
            <Button
              className="loginbtn"
              onClick={() => {
                this.touserlogin();
              }}
            >
              下一步
            </Button>
          </div>
        </>
      );
    }
    return (
      <div className="login_page">
        <div className="logo_box" ref="logo_box">
          <div className="title_logo">
            <img src={require("../../assets/img/headerlogo.png")} alt="" />
            <span>芸信·非法网站监测平台</span>
          </div>
          {loginbox}
          <div className="version ">杭州芸品绿信息科技有限公司 V1.0</div>
        </div>
      </div>
    );
  }
  //发送验证码
  async sendvarifiactoncode() {
    let { accountnumber, numtime } = this.state;
    if (accountnumber.length === 0) {
      $.error("请输入账号");
      return;
    }
    this.timer = setInterval(() => {
      this.setState({
        numtime: (numtime -= 1),
      });
    }, 1000);
    this.setState({
      isclicksend: true,
      issendvarifiation: false,
    });
    await smsverification(this, { mobile: accountnumber });
  }
  //用户登陆
  async userlogin() {
    let { history } = this.props;
    let { accountnumber, password, varifiactoncode } = this.state;
    if (accountnumber.length === 0) {
      $.error("账号/密码不可为空，请重新输入！");
      return;
    }
    if (password.length === 0) {
      $.error("账号/密码不可为空，请重新输入！");
      return;
    }
    if (varifiactoncode.length === 0) {
      $.error("请输入验证码");
      return;
    }
    let res = await userloginapi(this, {
      mobile: accountnumber,
      password: password,
      smscode: varifiactoncode,
      e_name: localStorage.getItem("ename"),
    });
    if (res.data.code === 200) {
      localStorage.setItem("mobiles", accountnumber);
      localStorage.setItem("field", res.data.field);
      localStorage.setItem("menu", res.data.menu);
      localStorage.setItem("uid", res.data.uid);
      localStorage.setItem("username", res.data.username);
      sessionStorage.setItem("handletime", +new Date());
      // localStorage.setItem("pwd", Base64.encode(password));
      sessionStorage.setItem("token", res.data.message);
      $.success("登录成功");
      let newmenu = res.data.menu.split(",").sort((a, b) => {
        return a - b;
      });

      let pushrouter = routes[1].children.filter((res) => {
        if (res.cdoe === newmenu[0]) {
          return res;
        }
      });
      history.push(pushrouter[0].path);
    } else {
      $.error(res.data.message);
    }
  }
  //返回域登陆
  backdomine() {
    this.setState(
      {
        isclicksend: false,
        isuserlogin: false,
        issendvarifiation: true,
        accountnumber: "",
        password: "",
        varifiactoncode: "",
        numtime: 60,
        region: localStorage.getItem("ename"),
      },
      () => {
        localStorage.clear();
        clearInterval(this.timer);
      }
    );
  }
  //域登陆
  async touserlogin() {
    if (this.state.region.length === 0) {
      $.error("请输入域名");
      return;
    } else {
      let res = await loginregion(this, { e_name: this.state.region });
      if (res.data.code === 200) {
        localStorage.setItem("ename", res.data.ename);
        localStorage.setItem("domainid", res.data.domainid);
        localStorage.setItem("city", res.data.city);
        localStorage.setItem(
          "iscity",
          res.data.city.split("-").length > 2 ? 1 : 0
        );
        this.setState({
          isuserlogin: true,
        });
      } else {
        $.error("域名不存在，请重新输入");
      }
    }
  }
  handleresize() {
    this.refs.logo_box.style.height =
      this.refs.logo_box.clientWidth * 0.176 +
      this.refs.logo_box.clientWidth +
      "px";
  }
}
export default connect(
  (state) => {
    return {};
  },
  (dispatch) => {
    return bindActionCreators(Action, dispatch);
  }
)(Login);
