import React from "react";
import "./index.scss";
import { Input, Button, Drawer, Modal, Checkbox } from "antd";
import { PlusOutlined, CloseOutlined } from "@ant-design/icons";
import Mytable from "../../component/mytable/index";
import $ from "../../utils/index";
import { query_role, add_role, update_role, del_roel } from "./request";
import Allcheckout from "../../component/allcheck";
import { menumsg, fieldmsg, fieldmsgs } from "../../utils/dictionaries.js";

class Role extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentpage: 1,
      pagesize: 13,
      count: 0,
      searchipt: "",
      searchipts: "",
      name: "",
      role_id: "",
      isdeletecon: false,
      handledrawer: false,
      isaddmsg: true,
      plainOptions: menumsg.filter((item) => {
        return localStorage.getItem("menu").split(",").includes(item.value);
      }),
      options: localStorage.getItem("iscity") === "0" ? fieldmsg : fieldmsgs,
      defaultplainOptions: [],
      fieldmsgcheckd: [],
      dataname: [
        { prop: "key", label: "#", width: "16.6%", width: "16.6%" },
        { prop: "role_name", label: "角色名称", width: "16.6%" },
        { prop: "create_times", label: "创建时间", width: "16.6%" },
        { prop: "menu_id_list", label: "菜单权限", maxWidth: "400px" },
        { prop: "field_list", label: "字段权限", maxWidth: "300px" },
      ],
      datamsg: [],
    };
  }
  //表格点击编辑
  updata_row = (val) => {
    this.setState({
      role_id: val.role_id,
      name: val.role_name,
      handledrawer: true,
      isaddmsg: false,
      defaultplainOptions: menumsg
        .filter((item) => {
          if (val.menu_id_list.split(",").indexOf(item.label) > -1) {
            return item.value;
          }
        })
        .map((res) => {
          return res.value;
        }),
      fieldmsgcheckd: fieldmsg
        .filter((item) => {
          if (val.field_list.split(",").indexOf(item.label) > -1) {
            return item.value;
          }
        })
        .map((res) => {
          return res.value;
        }),
    });
  };
  //表格点击删除
  delete_row = (val) => {
    this.setState({
      role_id: val.role_id,
      name: val.role_name,
      isdeletecon: true,
    });
  };
  //查询
  getquery_role = async () => {
    let res = await query_role(this, {
      ename: localStorage.getItem("ename"),
      role_name: this.state.searchipts,
      currentpage: this.state.currentpage,
      pagesize: this.state.pagesize,
    });
    if (res.data.code === 200) {
      this.setState({
        count: res.data.data_count,
        datamsg: res.data.content_list.map((res, index) => {
          res.key = index + 1;
          res.create_times = res.create_time;
          let fieldmsgres = fieldmsg.filter((item) => {
            return res.field_list.split(",").indexOf(item.value) > -1;
          });
          res.field_list = fieldmsgres
            .map((res) => {
              return res.label;
            })
            .join(",");
          let menumsgres = menumsg.filter((item) => {
            return res.menu_id_list.split(",").indexOf(item.value) > -1;
          });
          res.menu_id_list = menumsgres
            .map((res) => {
              return res.label;
            })
            .join(",");
          return res;
        }),
      });
    }
  };
  //删除
  rmconfig = async () => {
    let res = await del_roel(this, {
      role_id: this.state.role_id,
    });
    if (res.data.code === 200) {
      $.success(res.data.message);
      this.setState(
        {
          role_id: "",
          name: "",
          isdeletecon: false,
        },
        () => {
          this.getquery_role();
        }
      );
    } else {
      $.error(res.data.message);
      this.setState(
        {
          role_id: "",
          name: "",
          isdeletecon: false,
        },
        () => {
          this.getquery_role();
        }
      );
    }
  };
  //添加确定按钮
  configdraw = async () => {
    //确定添加
    if (this.state.name.length === 0) {
      $.error("请填写角色名称");
      return;
    }
    if (this.state.defaultplainOptions.length === 0) {
      $.error("请选择菜单权限");
      return;
    }
    if (this.state.fieldmsgcheckd.length === 0) {
      $.error("请选择字段权限");
      return;
    }

    //确定添加
    if (this.state.isaddmsg) {
      let res = await add_role(this, {
        ename: localStorage.getItem("ename"),
        role_name: this.state.name,
        menu_id_list: this.state.defaultplainOptions.join(","),
        field_list: this.state.fieldmsgcheckd.join(","),
      });
      if (res.data.code === 200) {
        $.success("角色添加成功");
        this.setState(
          {
            defaultplainOptions: [],
            fieldmsgcheckd: [],
            name: "",
            handledrawer: false,
          },
          () => {
            this.getquery_role();
          }
        );
      } else {
        $.error(res.data.message);
      }
    } else {
      //确定修改
      let res = await update_role(this, {
        ename: localStorage.getItem("ename"),
        role_id: this.state.role_id,
        role_name: this.state.name,
        menu_id_list: this.state.defaultplainOptions.join(","),
        field_list: this.state.fieldmsgcheckd.join(","),
      });
      if (res.data.code === 200) {
        $.success("角色修改成功");
        this.setState(
          {
            defaultplainOptions: [],
            fieldmsgcheckd: [],
            name: "",
            role_id: "",
            handledrawer: false,
          },
          () => {
            this.getquery_role();
          }
        );
      } else {
        $.error(res.data.message);
      }
    }
  };
  checkoptopns = (checkedValues) => {
    this.setState({
      defaultplainOptions: checkedValues,
    });
  };
  render() {
    let { searchipt, isaddmsg, defaultplainOptions } = this.state;
    return (
      <div className="role_page">
        <Modal
          visible={this.state.isdeletecon}
          footer={null}
          width={330}
          bodyStyle={{ height: 220 }}
          keyboard={true}
          maskClosable={false}
          wrapClassName={"domin_page_deletederlog"}
          onCancel={() => {
            this.setState({
              username: "",
              name: "",
              password: "",
              mobile: "",
              isdeletecon: false,
            });
          }}
          centered={true}
        >
          <div className="header">温馨提示</div>
          <div className="tip">
            确认删除角色<span>{this.state.name}</span> ？
          </div>
          <div className="footer">
            <Button onClick={this.rmconfig}>确认</Button>
            <Button
              onClick={() => {
                this.setState({
                  isdeletecon: false,
                });
              }}
            >
              取消
            </Button>
          </div>
        </Modal>
        <Drawer
          placement="right"
          closable={false}
          onClose={() => {
            this.setState({
              defaultplainOptions: [],
              fieldmsgcheckd: [],
              name: "",
              handledrawer: false,
            });
          }}
          visible={this.state.handledrawer}
          width={500}
        >
          <div className="drawer_containers">
            <div className="headers">
              {isaddmsg ? "添加角色" : "编辑角色"}
              <CloseOutlined
                style={{ cursor: "pointer" }}
                className="closebtn"
                onClick={() => {
                  this.setState({
                    defaultplainOptions: [],
                    fieldmsgcheckd: [],
                    name: "",
                    handledrawer: false,
                  });
                }}
              />
            </div>
            <div className="drawer_main">
              <div className="drawer_item">
                <div>角色名称</div>
                <div
                  onClick={(e) => {
                    if (e.target.tagName === "path") {
                      this.setState({
                        name: "",
                      });
                    }
                  }}
                >
                  <Input
                    placeholder="请输入用户名"
                    allowClear={true}
                    value={this.state.name}
                    onInput={(e) => {
                      this.setState({
                        name: e.target.value.replace(/\s/g).slice(0, 10),
                      });
                    }}
                  />
                </div>
              </div>
              <div className="drawer_item">
                <div>菜单权限</div>
                <div className="checkboxgroup">
                  <Checkbox.Group
                    options={this.state.plainOptions}
                    value={defaultplainOptions}
                    onChange={this.checkoptopns}
                  />
                </div>
              </div>
              <div className="drawer_item">
                <div>字段权限</div>
                <div className="allcheck">
                  <Allcheckout
                    fieldmsg={this.state.options}
                    defaultfieldmsg={this.state.fieldmsgcheckd}
                    watchdatachange={(val) => {
                      this.setState({
                        fieldmsgcheckd: val,
                      });
                    }}
                  />
                </div>
              </div>
            </div>

            <div className="buttonboxs">
              <Button
                onClick={() => {
                  this.configdraw();
                }}
              >
                确定
              </Button>
              <Button
                onClick={() => {
                  this.setState({
                    defaultplainOptions: [],
                    fieldmsgcheckd: [],
                    name: "",
                    handledrawer: false,
                  });
                }}
              >
                取消
              </Button>
            </div>
          </div>
        </Drawer>
        <div className="tip">角色管理</div>
        <div className="role_pagesearch">
          <div>
            <div>角色名称</div>
            <div
              onClick={(e) => {
                if (e.target.tagName === "path") {
                  this.setState({
                    searchipt: "",
                  });
                }
              }}
            >
              <Input
                placeholder="请输入角色名称"
                value={searchipt}
                allowClear={true}
                onInput={(e) => {
                  this.setState({
                    searchipt: e.target.value.replace(/\s/g, "").slice(0, 10),
                  });
                }}
              />
            </div>
          </div>
          <div>
            <div style={{ opacity: "0" }}>123</div>
            <div>
              <Button
                onClick={() => {
                  this.setState(
                    {
                      searchipts: this.state.searchipt,
                      currentpage: 1,
                    },
                    () => {
                      this.getquery_role();
                    }
                  );
                }}
              >
                查询
              </Button>
            </div>
          </div>
          <div>
            <div style={{ opacity: "0" }}>123</div>
            <div>
              <Button
                onClick={() => {
                  this.setState(
                    {
                      searchipt: "",
                      currentpage: 1,
                    },
                    () => {
                      this.getquery_role();
                    }
                  );
                }}
              >
                重置
              </Button>
            </div>
          </div>
          <div
            className="add"
            onClick={() => {
              this.setState({ handledrawer: true, isaddmsg: true });
            }}
          >
            <Button>
              <PlusOutlined />
              添加
            </Button>
          </div>
        </div>
        <div className="main" ref="massgebox">
          <div ref="tablebox">
            <Mytable
              dataname={this.state.dataname}
              datamsg={this.state.datamsg}
              currentpage={this.state.currentpage}
              pagesize={this.state.pagesize}
              count={this.state.count}
              watchpagechange={(val) => {
                this.setState(
                  {
                    currentpage: val,
                  },
                  () => {
                    this.getquery_role();
                  }
                );
              }}
              handlebtn={true}
              name="setting"
              viewtheimage={(val) => {
                this.updata_row(val);
              }}
              aboutthedisposal={(val) => {
                this.delete_row(val);
              }}
            ></Mytable>
          </div>
        </div>
      </div>
    );
  }
  settabbox = () => {
    if (this.refs.tablebox) {
      this.refs.tablebox.style.height = document.body.clientHeight - 250 + "px";
      this.setState(
        {
          pagesize: Math.floor(
            (Math.floor(document.body.clientHeight) - 250) / 50
          ),
        },
        () => {
          this.getquery_role();
        }
      );
    }
  };
  componentDidUpdate() {
    if (this.refs.tablebox) {
      this.refs.tablebox.style.height = document.body.clientHeight - 250 + "px";
    }
  }
  componentDidMount() {
    this.settabbox();
    window.onresize = $.debounce(this.settabbox, 1000);
  }
  componentWillUnmount() {
    window.onresize = null;
  }
}
export default Role;
