import React, { Component } from "react";
import { Select } from "antd";
import city from "./city.json";
import "./index.scss";
const { Option } = Select;
const provinceData = Object.keys(city);
const cityDatas = provinceData.map((item, index) => {
  let obj = {};
  obj[item] = Object.keys(Object.values(city)[index]);
  return obj;
});
let objss = {};
provinceData.map((item, index) => {
  Object.assign(objss, Object.values(city)[index]);
});
let objs = {};
cityDatas.map((items) => {
  Object.assign(objs, items);
});
export default class Threelinkage extends Component {
  state = {
    defaultarea: "",
    cities: Object.keys(objss),
    secondCity: "全部",
    threeCity: [],
    threeCityval: "全部",
    onecity: "全部",
  };

  onSecondCityChange = (value) => {
    this.setState({
      secondCity: value,
      defaultarea: this.state.onecity + "-" + value,
      threeCity: objss[value],
      threeCityval: "全部",
    });
    this.props.areavalue(this.state.onecity + "-" + value);
  };
  onthreeCityChange = (value) => {
    if (value !== "全部") {
      this.setState({
        threeCityval: value,
        defaultarea:
          this.state.onecity + "-" + this.state.secondCity + "-" + value,
      });
      this.props.areavalue(
        this.state.onecity + "-" + this.state.secondCity + "-" + value
      );
      if (value === "市本级") {
        this.props.chooseSamelevel(this.state.threeCity.slice(2).join(","));
      }
    } else {
      this.setState({
        threeCityval: value,
        defaultarea: this.state.onecity + "-" + this.state.secondCity,
      });
      this.props.areavalue(this.state.onecity + "-" + this.state.secondCity);
    }
  };
  render() {
    const { cities, secondCity, threeCity, threeCityval, onecity } = this.state;
    return (
      <div className="levelthreelinkage">
        <Select
          value={secondCity}
          onChange={this.onSecondCityChange}
          disabled={true}
        >
          {cities.map((city) => (
            <Option key={city}>{city}</Option>
          ))}
        </Select>
        -
        <Select
          value={threeCityval}
          disabled={threeCityval.length > 0 ? false : true}
          onChange={this.onthreeCityChange}
        >
          {threeCity.map((city) => (
            <Option key={city}>{city}</Option>
          ))}
        </Select>
      </div>
    );
  }
  componentDidMount() {
    let one = this.props.city.split("-")[0];
    let two = this.props.city.split("-")[1];
    let three = this.props.city.split("-")[2];
    this.setState({
      cities: Object.keys(objss),
      secondCity: two ? two : "全部",
      threeCity: two ? ["全部", "市本级"].concat(objss[two]) : [],
      threeCityval: three ? three : "全部",
      onecity: one ? one : "全部",
    });
  }
  componentWillReceiveProps(val) {
    let one = val.city.split("-")[0];
    let two = val.city.split("-")[1];
    let three = val.city.split("-")[2];

    this.setState({
      cities: Object.keys(objss),
      secondCity: two ? two : "全部",
      threeCity: two ? ["全部", "市本级"].concat(objss[two]) : [],
      threeCityval: three ? three : "全部",
      onecity: one ? one : "全部",
    });
  }
}
