import React from "react";
import "./index.scss";
import { Input, Button, DatePicker, ConfigProvider } from "antd";
import Mytable from "../../component/mytable/index";
import { view_log_info } from "./request";
import zh_CN from "antd/lib/locale-provider/zh_CN";
import moment from "moment";
import $ from "../../utils/index";
const { RangePicker } = DatePicker;
class Viewlog extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentpage: 1,
      pagesize: 13,
      count: 0,
      domains: "",
      unitTypes: [
        moment(moment().startOf("day").subtract(30, "days")).format(
          "YYYY-MM-DD"
        ),
        moment(moment().endOf("day")).format("YYYY-MM-DD"),
      ],
      useraccounts: "",
      recordnums: "",
      domain: "",
      useraccount: "",
      recordnum: "",
      timers: [
        moment().startOf("day").subtract(30, "days"),
        moment().endOf("day"),
      ],
      unitType: [
        moment(moment().startOf("day").subtract(30, "days")).format(
          "YYYY-MM-DD"
        ),
        moment(moment().endOf("day")).format("YYYY-MM-DD"),
      ],
      dataname: [
        { prop: "key", label: "#", width: "14.6%" },
        { prop: "ename", label: "登录域", width: "14.6%" },
        { prop: "username", label: "账号", width: "14.6%" },
        { prop: "phone", label: "手机号", width: "14.6%" },
        { prop: "ip", label: "IP", width: "14.6%" },
        { prop: "licensekey", label: "备案号", width: "14.6%" },
        {
          prop: "r_time",
          label: "查询时间",
          width: "14.6%",
        },
      ],
      datamsg: [],
    };
  }

  //查询
  getview_log_info = async () => {
    let res = await view_log_info(this, {
      currentPage: this.state.currentpage,
      limitCount: this.state.pagesize,
      ename: this.state.domains,
      username: this.state.useraccounts,
      licensekey: this.state.recordnums,
      s_time: this.state.unitTypes[0],
      e_time: this.state.unitTypes[1],
    });
    if (res.data.status === "200") {
      this.setState({
        count: res.data.data_count,
        datamsg: res.data.detailListInfo.map((res, index) => {
          res.create_times = res.create_time;
          res.key = index + 1;
          return res;
        }),
      });
    }
  };

  render() {
    let { domain, useraccount, recordnum, unitType } = this.state;
    return (
      <div className="user_page">
        <div className="user_pagesearch">
          <div>
            <div>登录域</div>
            <div
              onClick={(e) => {
                if (e.target.tagName === "path") {
                  this.setState({
                    domain: "",
                  });
                }
              }}
            >
              <Input
                placeholder="请输入登录域"
                value={domain}
                allowClear={true}
                onInput={(e) => {
                  this.setState({
                    domain: e.target.value.replace(/[^a-zA-Z]/g, ""),
                  });
                }}
              />
            </div>
          </div>
          <div>
            <div>账号</div>
            <div
              onClick={(e) => {
                if (e.target.tagName === "path") {
                  this.setState({
                    useraccount: "",
                  });
                }
              }}
            >
              <Input
                placeholder="请输入账号"
                value={useraccount}
                allowClear={true}
                onInput={(e) => {
                  this.setState({
                    useraccount: e.target.value.replace(
                      /[\u4E00-\u9FA5\uF900-\uFA2D\u0020/s]/g,
                      ""
                    ),
                  });
                }}
              />
            </div>
          </div>
          <div>
            <div>备案号</div>
            <div
              onClick={(e) => {
                if (e.target.tagName === "path") {
                  this.setState({
                    recordnum: "",
                  });
                }
              }}
            >
              <Input
                placeholder="请输入备案号"
                value={recordnum}
                allowClear={true}
                onInput={(e) => {
                  this.setState({
                    recordnum: e.target.value.replace(/\s/g, ""),
                  });
                }}
              />
            </div>
          </div>
          <div className="lookupTime">
            <div>查询时间</div>
            <div
              onClick={(e) => {
                if (e.target.tagName === "path") {
                  this.setState({
                    timers: null,
                    unitType: [],
                  });
                }
              }}
            >
              <ConfigProvider locale={zh_CN}>
                <RangePicker
                  disabledDate={(current) => {
                    if (!current) {
                      return false;
                    } else {
                      return (
                        moment() - 2592000000 > current || current > moment()
                      );
                    }
                  }}
                  value={this.state.timers}
                  format={"YYYY-MM-DD"}
                  style={{ width: "300px" }}
                  onChange={(value) => {
                    if (value) {
                      this.setState({
                        timers: value,
                        unitType: [
                          moment(value[0]).format("YYYY-MM-DD"),
                          moment(value[1]).format("YYYY-MM-DD"),
                        ],
                      });
                    } else {
                      this.setState({
                        timers: null,
                        unitType: [],
                      });
                    }
                  }}
                />
              </ConfigProvider>
            </div>
          </div>
          <div className="buttonboxserchreset">
            <div>
              <div style={{ opacity: "0" }}>123</div>
              <div>
                <Button
                  onClick={() => {
                    this.setState(
                      {
                        domains: domain,
                        useraccounts: useraccount,
                        recordnums: recordnum,
                        unitTypes: unitType,
                        currentpage: 1,
                      },
                      () => {
                        this.getview_log_info();
                      }
                    );
                  }}
                >
                  查询
                </Button>
              </div>
            </div>
            <div>
              <div style={{ opacity: "0" }}>123</div>
              <div>
                <Button
                  onClick={() => {
                    this.setState({
                      domain: "",
                      useraccount: "",
                      recordnum: "",
                      timers: [
                        moment().startOf("day").subtract(30, "days"),
                        moment().endOf("day"),
                      ],
                      unitType: [
                        moment(
                          moment().startOf("day").subtract(30, "days")
                        ).format("YYYY-MM-DD"),
                        moment(moment().endOf("day")).format("YYYY-MM-DD"),
                      ],
                    });
                  }}
                >
                  重置
                </Button>
              </div>
            </div>
          </div>
        </div>
        <div className="main" ref="massgebox">
          <div ref="tablebox">
            <Mytable
              dataname={this.state.dataname}
              datamsg={this.state.datamsg}
              handlebtn={false}
              name="setting"
              currentpage={this.state.currentpage}
              pagesize={this.state.pagesize}
              count={this.state.count}
              watchpagechange={(val) => {
                this.setState(
                  {
                    currentpage: val,
                  },
                  () => {
                    this.getview_log_info();
                  }
                );
              }}
            ></Mytable>
          </div>
        </div>
      </div>
    );
  }
  settabbox = () => {
    if (this.refs.tablebox) {
      this.refs.tablebox.style.height = document.body.clientHeight - 330 + "px";
      this.setState(
        {
          pagesize: Math.floor(
            (Math.floor(document.body.clientHeight) - 250) / 50
          ),
        },
        () => {
          this.getview_log_info();
        }
      );
    }
  };
  componentDidUpdate() {
    if (this.refs.tablebox) {
      this.refs.tablebox.style.height = document.body.clientHeight - 330 + "px";
    }
  }
  componentDidMount() {
    this.settabbox();
    window.onresize = $.debounce(this.settabbox, 1000);
  }
  componentWillUnmount() {
    window.onresize = null;
  }
}
export default Viewlog;
