import React from "react";
import "./index.scss";
import { Layout, Dropdown, Modal, Input, Button } from "antd";
import { CaretDownOutlined, UserOutlined } from "@ant-design/icons";
import RouterView from "../../router/router";
import { Link } from "react-router-dom";
import $ from "../../utils/index";
import { update_pwd } from "./request";
import routes from "../../router/routerConfig";
const { Header, Content } = Layout;
class Layoutmin extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currenttab: 1,
      changepwsdialog: false,
      outloginval: false,
      oldpwd: "",
      newpwd: "",
      newpwds: "",
      menu: localStorage.getItem("menu")
        ? localStorage.getItem("menu").split(",")
        : [],
    };
  }
  render() {
    const menu = (
      <div className="layoutdownselectfather">
        {this.state.menu.includes("40004") &&
          localStorage.getItem("ename") === "ypl" && (
            <Link to="/index/handeldomin">
              <div className="layoutdownselect">组织管理</div>
            </Link>
          )}
        {this.state.menu.includes("40004") && (
          <Link to="/index/handelrole">
            <div className="layoutdownselect">角色管理</div>
          </Link>
        )}
        {this.state.menu.includes("40004") && (
          <Link to="/index/handeluser">
            <div className="layoutdownselect">用户管理</div>
          </Link>
        )}
      </div>
    );
    const menus = (
      <div className="layoutdownselectfathers">
        <div
          className="layoutdownselects"
          onClick={() => {
            this.changepws();
          }}
        >
          修改密码
        </div>
        <div
          className="layoutdownselects"
          onClick={() => {
            this.openoutlogin();
          }}
        >
          退出登录
        </div>
      </div>
    );
    return (
      <div className="layout_page">
        <Modal
          visible={this.state.changepwsdialog}
          footer={null}
          width={500}
          keyboard={true}
          wrapClassName={"changepwsdialog "}
          onCancel={() => {
            this.handleCancel();
          }}
          centered={true}
          bodyStyle={{ height: 388 }}
        >
          <div className="changepwsdialogtip">修改密码</div>
          <div className="changepwsdialogipt">
            <div>
              <span>*</span>原始密码
            </div>
            <Input
              placeholder="请输入原始密码"
              type="password"
              value={this.state.oldpwd}
              onInput={(e) => {
                this.setState({ oldpwd: e.target.value.replace(/\s/g, "") });
              }}
            ></Input>
          </div>
          <div className="changepwsdialogipt">
            <div>
              <span>*</span>新密码
            </div>
            <Input
              placeholder="请输入新密码"
              type="password"
              value={this.state.newpwd}
              onInput={(e) => {
                this.setState({ newpwd: e.target.value.replace(/\s/g, "") });
              }}
            ></Input>
          </div>
          <div className="changepwsdialogipt">
            <div>
              <span>*</span>重复新密码
            </div>
            <Input
              placeholder="请再次输入新密码"
              type="password"
              value={this.state.newpwds}
              onInput={(e) => {
                this.setState({ newpwds: e.target.value.replace(/\s/g, "") });
              }}
            ></Input>
          </div>
          <div className="changepwsdialogbtn">
            <Button onClick={this.handleCancel}>取消</Button>
            <Button onClick={this.changepwd}>确定</Button>
          </div>
        </Modal>
        <Modal
          visible={this.state.outloginval}
          footer={null}
          width={330}
          keyboard={true}
          wrapClassName={"outloginval "}
          onCancel={() => {
            this.handleCanceloutloginval();
          }}
          centered={true}
          bodyStyle={{ height: 220 }}
        >
          <div className="outloginvaltip">温馨提示</div>
          <div className="outloginvalmsg">确认退出登录？</div>
          <div className="changepwsdialogbtn">
            <Button
              onClick={() => {
                this.handleCanceloutloginval();
              }}
            >
              取消
            </Button>
            <Button
              onClick={() => {
                this.props.history.push("/login");
                this.setState({
                  outloginval: false,
                });
              }}
            >
              确定
            </Button>
          </div>
        </Modal>
        <Layout className="layout">
          <Header className="header">
            <div className="header_menu">
              <div className="header_menu_msg">
                <img src={require("../../assets/img/headerlogo.png")} alt="" />
                <span>芸信·非法网站监测平台</span>
              </div>
              <div className="header_menu_msg">
                {this.state.menu.includes("40001") && (
                  <Link to="/index/webinformation">
                    <div
                      className={
                        this.state.currenttab === 1
                          ? "header_menu_msg_list header_menu_msg_listactive"
                          : "header_menu_msg_list"
                      }
                      onClick={() => {
                        this.tab(1);
                      }}
                    >
                      网站信息
                    </div>
                  </Link>
                )}
                {this.state.menu.includes("40002") && (
                  <Link to="/index/urlscreenshots">
                    <div
                      className={
                        this.state.currenttab === 2
                          ? "header_menu_msg_list header_menu_msg_listactive"
                          : "header_menu_msg_list"
                      }
                      onClick={() => {
                        this.tab(2);
                      }}
                    >
                      非备案网址
                    </div>
                  </Link>
                )}
                {this.state.menu.includes("40003") && (
                  <Link to="/index/viewlog">
                    <div
                      className={
                        this.state.currenttab === 3
                          ? "header_menu_msg_list header_menu_msg_listactive"
                          : "header_menu_msg_list"
                      }
                      onClick={() => {
                        this.tab(3);
                      }}
                    >
                      查看日志
                    </div>
                  </Link>
                )}
                {this.state.menu.includes("40004") && (
                  <div
                    className={
                      this.state.currenttab === 4
                        ? "header_menu_msg_list header_menu_msg_listactive"
                        : "header_menu_msg_list"
                    }
                    onMouseOver={() => {
                      this.tab(4);
                    }}
                    onMouseLeave={() => {
                      this.mousetab();
                    }}
                  >
                    <Dropdown
                      overlay={menu}
                      trigger={["hover"]}
                      placement="bottomCenter"
                    >
                      <div
                        className="ant-dropdown-link"
                        onClick={(e) => e.preventDefault()}
                      >
                        系统设置
                      </div>
                    </Dropdown>
                  </div>
                )}
              </div>
              <div className="header_menu_msg">
                <Dropdown
                  overlay={menus}
                  trigger={["hover"]}
                  placement="bottomRight"
                >
                  <div
                    className="ant-dropdown-link"
                    onClick={(e) => e.preventDefault()}
                  >
                    <div className="leftline"></div>
                    <UserOutlined />
                    {localStorage.getItem("username")}
                    <CaretDownOutlined
                      style={{
                        fontSize: "10px",
                        marginLeft: "5px",
                        marginTop: "3px",
                      }}
                    />
                  </div>
                </Dropdown>
              </div>
            </div>
          </Header>
          <Content>
            <div className="site-layout-content">
              <RouterView list={this.props.children}></RouterView>
            </div>
          </Content>
        </Layout>
      </div>
    );
  }
  componentDidMount() {
    this.props.history.listen(() => {
      this.mousetab();
    });
    this.mousetab();
  }
  //修改密码
  changepwd = async () => {
    if (!this.state.oldpwd) {
      $.error("请输入原密码");
      return;
    }
    if (!this.state.newpwd) {
      $.error("请输入新密码");
      return;
    }
    if (this.state.newpwd !== this.state.newpwds) {
      $.error("两次密码不一致，请重新输入！");
      return;
    }
    let res = await update_pwd(this, {
      user_id: localStorage.getItem("uid"),
      password: this.state.oldpwd,
      new_password: this.state.newpwd,
    });
    if (res.data.code === 200) {
      $.success("修改密码成功");
      this.props.history.push("/login");
      // this.setState({
      //   changepwsdialog: false,
      //   oldpwd: "",
      //   newpwd: "",
      //   newpwds: "",
      // });
    } else {
      $.error(res.data.message);
    }
  };
  //退出登录
  handleCanceloutloginval() {
    this.setState({
      outloginval: false,
    });
  }
  openoutlogin() {
    this.setState({
      outloginval: true,
    });
  }
  //修改密码
  handleCancel = () => {
    this.setState({
      changepwsdialog: false,
      oldpwd: "",
      newpwd: "",
      newpwds: "",
    });
  };
  changepws() {
    this.setState({
      changepwsdialog: true,
    });
  }
  //路由跳转
  tab(val) {
    this.setState({
      currenttab: val,
    });
  }
  //鼠标离开后判断是否选中
  mousetab() {
    if (this.props.history.location.pathname === "/index/webinformation") {
      this.setState({
        currenttab: 1,
      });
    } else if (
      this.props.history.location.pathname === "/index/urlscreenshots"
    ) {
      this.setState({
        currenttab: 2,
      });
    } else if (this.props.history.location.pathname === "/index/viewlog") {
      this.setState({
        currenttab: 3,
      });
    } else {
      this.setState({
        currenttab: 4,
      });
    }
  }
}
export default Layoutmin;
