import React from "react";
import "./index.scss";
import $ from "../../utils/index";
import { Pagination, Table, Button } from "antd";
const { Column } = Table;
class Input extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      newdataname: [],
      isscrollnum: false,
      iptcurrent: "",
      arr: [],
    };
  }
  componentWillMount() {
    let scrollnum = 0;
    if (this.props.dataname) {
      let result = this.props.dataname.map((res) => {
        res.minwidth = res.label.length * 16;
        scrollnum += res.minwidth + 10;
        return res;
      });
      this.setState({
        newdataname: result,
        scrollnum: scrollnum,
      });
    }
    this.tablecontent();
  }
  tablecontent = () => {
    setTimeout(() => {
      let dom = document.querySelector(".ant-table-content");
      if (dom) {
        let showBtn = dom.clientWidth < dom.scrollWidth;
        if (showBtn) {
          this.setState({
            isscrollnum: true,
          });
        } else {
          this.setState({
            isscrollnum: false,
          });
        }
      }
    });
  };
  handlemodlehovertitle(dom) {
    dom.onmouseover = (e) => {
      if (e.target.nodeName === "SPAN") {
        if (e.target.firstChild.nodeType === 3) {
          if (e.target.parentNode.className === "ant-table-cell") {
            let showBtn = e.target.clientWidth < e.target.scrollWidth;
            if (showBtn) {
              e.target.title = e.target.innerHTML;
            }
            window.addEventListener("resize", () => {
              let showTest =
                e.target.parentNodeclientWidth < e.target.parentNodescrollWidth;
              if (showTest) {
                e.target.title = e.target.innerHTML;
              }
            });
          }
        }
      }
    };
  }
  componentDidMount() {
    let com_mytabe = document.querySelector(".com_mytabe");
    let tablemsgbox = document.querySelector(".tablemsgbox");

    if (com_mytabe) {
      this.handlemodlehovertitle(com_mytabe);
    }
    if (tablemsgbox) {
      this.handlemodlehovertitle(tablemsgbox);
    }
    this.tablecontent();
  }
  componentWillUnmount() {
    this.refs.tablefotherbox.onresize = null;
  }
  componentWillReceiveProps(val) {
    if (val.datamsg.length > 0) {
      let arr = [];
      val.datamsg.map((res) => {
        res.website_type && arr.push(res.website_type.split(",").length);
      });
      this.setState({
        mathmaxnum: Math.max(...arr),
      });
    }
    this.tablecontent();
  }
  render() {
    let {
      havepagiation = true,
      name,
      completedata,
      viewtheimage,
      aboutthedisposal,
    } = this.props;
    let { newdataname, isscrollnum, scrollnum } = this.state;
    return (
      <div
        className="com_mytabe"
        ref="tablefotherbox"
        style={{ overflow: "hidden", paddingRight: "3px" }}
      >
        {this.props.datamsg.length > 0 && (
          <Table
            empty-text="暂无数据"
            dataSource={this.props.datamsg && this.props.datamsg}
            bordered={false}
            onRow={this.onRow}
            pagination={false}
            scroll={
              isscrollnum && this.props.name !== "chuzhijilu"
                ? { x: true }
                : this.props.name === "chuzhijilu" && this.props.datamsg.length>7
                ? { y: 300 }
                : false
            }
            // scroll={{ x: true }}
          >
            {newdataname &&
              newdataname.map((item, index) => {
                return (
                  <Column
                    fixed={item.prop === "key" ? "left" : ""}
                    title={item.label}
                    key={item.label}
                    width={item.width}
                    render={(text, record, index) => {
                      return (
                        <span
                          className={"mytds " + item.prop}
                          style={{
                            color: "#252631",
                            maxWidth: item.maxWidth,
                            display: "block",
                            width:
                              item.prop === "website_type"
                                ? this.state.mathmaxnum * 30 + 100 + "px"
                                : "100%",
                          }}
                        >
                          {this.tableclone(record[item.prop], item.prop)}
                        </span>
                      );
                    }}
                  />
                );
              })}
            {this.props.handlebtn && (
              <Column
                title="操作"
                key="setting"
                fixed="right"
                render={(text, record, index) => {
                  return (
                    <>
                      {name === "datanamecity" && (
                        <div className=" mytds operation">
                          <span
                            style={{ cursor: "pointer" }}
                            onClick={() => {
                              viewtheimage(record);
                            }}
                          >
                            查看图片
                          </span>
                          <span style={{ color: "#D8DDE4" }}>|</span>
                          <span
                            style={{ cursor: "pointer" }}
                            onClick={() => {
                              aboutthedisposal(record);
                            }}
                          >
                            关于处置
                          </span>
                        </div>
                      )}
                      {name === "datanamecounty" && (
                        <div className=" mytds operation">
                          <span
                            style={{ cursor: "pointer" }}
                            onClick={() => {
                              viewtheimage(record);
                            }}
                          >
                            查看图片
                          </span>
                          <span style={{ color: "#D8DDE4" }}>|</span>
                          <span
                            style={{ cursor: "pointer" }}
                            onClick={() => {
                              aboutthedisposal(record);
                            }}
                          >
                            关于处置
                          </span>
                          <span style={{ color: "#D8DDE4" }}>|</span>
                          <span
                            style={{ cursor: "pointer" }}
                            onClick={() => {
                              completedata(record);
                            }}
                          >
                            查看完整数据
                          </span>
                        </div>
                      )}

                      {((name === "setting" &&
                        record.ename &&
                        record.ename !== "ypl") ||
                        (name === "setting" &&
                          record.role_name &&
                          record.role_name !== "管理员") ||
                        (name === "setting" &&
                          record.username &&
                          record.username !== "admin")) && (
                        <div className=" mytds operation">
                          <span
                            style={{ cursor: "pointer" }}
                            onClick={() => {
                              viewtheimage(record);
                            }}
                          >
                            编辑
                          </span>
                          <span style={{ color: "#D8DDE4" }}>|</span>
                          <span
                            onClick={() => {
                              aboutthedisposal(record);
                            }}
                            style={{ color: "#CB543E", cursor: "pointer" }}
                          >
                            删除
                          </span>
                          <span style={{ opacity: 0 }}>|</span>
                          <span style={{ opacity: 0 }}>查看完整数据</span>
                        </div>
                      )}
                    </>
                  );
                }}
              />
            )}
          </Table>
        )}
        {this.props.datamsg.length === 0 && (
          <div style={{ textAlign: "center", marginTop: "30px" }}>暂无数据</div>
        )}
        {havepagiation && this.props.count > this.props.pagesize && (
          <div className="mypagination">
            <div className="jumppageation">
              前往
              <input
                value={this.state.iptcurrent}
                onKeyDown={(e) => {
                  if (e.keyCode === 13 && this.state.iptcurrent) {
                    this.props.watchpagechange(+this.state.iptcurrent);
                  }
                }}
                onChange={(e) => {
                  this.setState({
                    iptcurrent:
                      e.target.value.replace(/[\D]/g, "") >
                      Math.ceil(this.props.count / this.props.pagesize)
                        ? Math.ceil(this.props.count / this.props.pagesize)
                        : e.target.value.replace(/[\D]/g, "") <= 0
                        ? ""
                        : e.target.value.replace(/[\D]/g, ""),
                  });
                }}
              />
              页
            </div>
            <Button
              disabled={this.props.currentpage === 1}
              style={{ marginRight: "10px" }}
              onClick={() => {
                this.props.watchpagechange(1);
              }}
            >
              首页
            </Button>
            <Pagination
              total={this.props.count}
              onChange={this.changepagecount}
              hideOnSinglePage={true}
              pageSize={this.props.pagesize}
              current={this.props.currentpage}
            />
            <Button
              disabled={
                this.props.currentpage ===
                Math.ceil(this.props.count / this.props.pagesize)
              }
              style={{ marginLeft: "10px" }}
              onClick={() => {
                this.props.watchpagechange(
                  Math.ceil(this.props.count / this.props.pagesize)
                );
              }}
            >
              末页
            </Button>
          </div>
        )}
      </div>
    );
  }
  tableclone = (val, valpropos) => {
    if (valpropos === "create_time" && val) {
      return <>{val.slice(5)}</>;
    }
    if (valpropos === "password" && val) {
      return (
        <>
          <span>·</span>
          <span>·</span>
          <span>·</span>
          <span>·</span>
          <span>·</span>
          <span>·</span>
          <span>·</span>
        </>
      );
    }
    if (valpropos === "city" && val) {
      return (
        <>{val.slice(val.indexOf("-") === -1 ? 0 : val.indexOf("-") + 1)}</>
      );
    }
    if (valpropos === "key" && val) {
      return (
        <>
          {this.props.name !== "chuzhijilu" &&
            val + (this.props.currentpage - 1) * this.props.pagesize}
        </>
      );
    }
    if (valpropos === "topDomain" && val) {
      return (
        <>
          <a href={"http://www." + val} target="_Blank">
            {val}
          </a>
        </>
      );
    }
    if (valpropos === "website_type" && val) {
      return (
        <>
          {val.split(",").map((res, index) => {
            return (
              <div
                key={index}
                onMouseEnter={(e) => {
                  if (e.currentTarget.children[1]) {
                    e.currentTarget.children[1].style.width =
                      res.length * 16 + "px";
                  }
                }}
                onMouseLeave={(e) => {
                  if (e.currentTarget.children[1]) {
                    e.currentTarget.children[1].style.width = "0px";
                  }
                }}
              >
                {res === "新闻资讯" && (
                  <span className="iconfont"> &#xe8a7;</span>
                )}
                {res === "音视频" && (
                  <span className="iconfont"> &#xe7da;</span>
                )}
                {res === "直播" && <span className="iconfont">&#xe78b;</span>}
                {res === "购物交易" && (
                  <span className="iconfont"> &#xe7cf;</span>
                )}
                {res === "互动论坛" && (
                  <span className="iconfont">&#xe8e8;</span>
                )}
                {res === "手机应用商店" && (
                  <span className="iconfont">&#xe7b2;</span>
                )}
                {res === "在线教育" && (
                  <span className="iconfont"> &#xe60d;</span>
                )}
                {res === "其他" && <span className="iconfont"> &#xe781;</span>}
                <span>
                  <b>{res}</b>
                </span>
              </div>
            );
          })}
        </>
      );
    }
    if (valpropos === "system_decide") {
      return (
        <>
          {val === "正常网站" && (
            <span style={{ color: "#45A720" }}>正常网站</span>
          )}
          {val === "风险网站" && (
            <span style={{ color: "#CB543E" }}>风险网站</span>
          )}
          {val === "未判定" && <span style={{ color: "#B3B3B7" }}>未判定</span>}
          {val !== "风险网站" && val !== "正常网站" && val !== "未判定" && (
            <span style={{ color: "#CB543E" }}>{val}</span>
          )}
        </>
      );
    }
    if (valpropos === "handle_result") {
      return (
        <>
          {val === "未处置" && <span style={{ color: "#B3B3B7" }}>未处置</span>}
          {val === "正常网站" && (
            <span style={{ color: "#45A720" }}>正常网站</span>
          )}
          {val === "风险网站" && (
            <span style={{ color: "#CB543E" }}>风险网站</span>
          )}
          {val !== "风险网站" && val !== "未处置" && val !== "正常网站" && (
            <span style={{ color: "#CB543E" }}>{val}</span>
          )}
        </>
      );
    }
    return val ? val : "--";
  };
  //分页
  changepagecount = (pageNumber) => {
    this.props.watchpagechange(pageNumber);
  };
}
export default Input;
