//网站信息
let get_site_info = (_this, params) => {
  return _this.get("/data/get_site_info", params);
};
//区县的网站信息
let county_site_info = (_this, params) => {
  return _this.get("/data/county_site_info", params);
};

//查看图片
let view_pic = (_this, params) => {
  return _this.get("/data/view_pic", params);
};
//去处置
let to_dispose = (_this, params) => {
  return _this.post("/data/to_dispose", params);
};
//处置记录
let dispose_info = (_this, params) => {
  return _this.get("/data/dispose_info", params);
};
//图片模式列表
let picture_mode = (_this, params) => {
  let api =
    localStorage.getItem("iscity") === "1"
      ? "/data/county_picture_mode"
      : "/data/picture_mode";
  return _this.get(api, params);
};
export {
  get_site_info,
  view_pic,
  to_dispose,
  dispose_info,
  county_site_info,
  picture_mode,
};
