import React, { Component } from "react";
import { Button, Input } from "antd";
import { view_data } from "./request";
import { smsverification } from "../../pages/login/request";
import $ from "../../utils/index";
import publicIp from "public-ip";
(async () => {
  window.user_ip = await publicIp.v4();
})();
export default class Smsverification extends Component {
  state = {
    iserroryanzhengma: false,
    numtime: 60,
    iptval: "",
    issendvarifiation: false,
    openressetnewsent: false,
    istimerview: false,
  };
  render() {
    let { closesmsverification } = this.props;
    return (
      <>
        <div className="derivedresults_modaltip">账号验证</div>
        <div className="derivedresults_modalbody">
          <div className="smsverificatons_box">
            <div>
              手机号：
              <span>{localStorage.getItem("mobiles")}</span>
            </div>
            <div>验证码</div>
            <div style={{ display: "flex" }}>
              <Input
                placeholder="请输入短信验证码"
                value={this.state.iptval}
                onInput={(e) => {
                  this.setState({
                    iptval: e.target.value.replace(/[^0-9]/g, "").slice(0, 6),
                  });
                }}
              />
              {!this.state.issendvarifiation && (
                <Button
                  onClick={() => {
                    this.verification();
                  }}
                >
                  发送验证码
                </Button>
              )}
              {this.state.openressetnewsent && (
                <Button
                  disabled={this.state.istimerview}
                  onClick={() => {
                    this.verification();
                  }}
                >
                  {this.state.istimerview && this.state.numtime + "秒"} 重新发送
                </Button>
              )}
            </div>
            <div>
              {this.state.iserroryanzhengma ? "验证码错误，请重新输入" : ""}
            </div>
            <div>
              <Button
                onClick={() => {
                  closesmsverification(false);
                }}
              >
                取消
              </Button>
              <Button
                onClick={() => {
                  this.configvalidation();
                }}
              >
                确定
              </Button>
            </div>
          </div>
        </div>
      </>
    );
  }
  componentWillReceiveProps() {
    clearInterval(this.timer);
    this.setState({
      issendvarifiation: false,
      iserroryanzhengma: false,
      openressetnewsent: false,
      numtime: 60,
      iptval: "",
    });
  }
  componentDidUpdate() {
    if (this.state.numtime === 0) {
      clearInterval(this.timer);
      this.setState({
        istimerview: false,
        numtime: 60,
      });
    }
  }
  verification = async () => {
    let { numtime } = this.state;
    this.timer = setInterval(() => {
      this.setState({
        numtime: (numtime -= 1),
      });
    }, 1000);
    this.setState({
      istimerview: true,
      issendvarifiation: true,
      openressetnewsent: true,
    });
    await smsverification(this, {
      mobile: localStorage.getItem("mobiles"),
    });
  };
  configvalidation = async () => {
    let { iptval } = this.state;
    if (iptval.length === 0) {
      $.error("请输入验证码");
      return;
    }
    if (this.props.smsverificationlicenseKey) {
      let res = await view_data(this, {
        mobiles: localStorage.getItem("mobiles"),
        uid: localStorage.getItem("uid"),
        licensekey:
          this.props.smsverificationlicenseKey &&
          this.props.smsverificationlicenseKey,
        smscode: iptval,
        user_ip: window.user_ip,
      });
      if (res.data.code === 200) {
        this.props.openentiredata(true);
        clearInterval(this.timer);
        this.setState({
          issendvarifiation: false,
          iserroryanzhengma: false,
          openressetnewsent: false,
          numtime: 60,
          iptval: "",
        });
      } else {
        this.setState({
          iserroryanzhengma: true,
        });
      }
    }
  };
}
