//网站截图 - 列表
let website_screenshot_info = (_this, params) => {
  return _this.get("/data/website_screenshot_info", params);
};
//网站截图-查看图片
let website_screenshot_view_pic = (_this, params) => {
  return _this.get("/data/website_screenshot_view_pic", params);
};

//网站截图-去处理
let website_screenshot_to_dispose = (_this, params) => {
  return _this.post("/data/website_screenshot_to_dispose", params);
};
//网站截图-处置记录
let website_screenshot_dispose_info = (_this, params) => {
  return _this.get("/data/website_screenshot_dispose_info", params);
};
export {
  website_screenshot_info,
  website_screenshot_view_pic,
  website_screenshot_to_dispose,
  website_screenshot_dispose_info,
};
