import React, { Component } from "react";
import { Upload, Button } from "antd";
import { FolderAddFilled } from "@ant-design/icons";
import axios from "axios";
import $ from "../../utils/index";
export default class Uploadmyself extends Component {
  state = {
    filename: "请选择",
    fileList: [],
    uploading: false,
    isfinish: false,
  };
  handleUpload = () => {
    const { fileList } = this.state;
    var filetype = fileList[0].name.match(/^(.*)(\.)(.{1,8})$/)[3];
    if (filetype !== "xls" && filetype !== "xlsx") {
      $.error("不是excel格式! ");
      return;
    }
    const { name = "wangzahnxinx" } = this.props;
    const formData = new FormData();
    fileList.forEach((file) => {
      formData.append("file", file);
    });
    formData.append("uid", localStorage.getItem("uid"));
    // 这里初始化source对象
    this.setState(
      {
        uploading: true,
        source: axios.CancelToken.source(),
      },
      () => {
        console.log(fileList);
        axios({
          url:
            name !== "wangzanjietu"
              ? "/api/data/site_upload_file"
              : "/api/data/website_screenshot_import_file",
          method: "POST",
          data: formData,
          cancelToken: this.state.source.token,
        })
          .then((res) => {
            if (res.data.status === 200) {
              this.setState({
                issuccess: true,
                isfinish: true,
              });
              $.success(res.data.message);
            } else {
              this.setState({
                issuccess: false,
                isfinish: true,
              });
              $.error(res.data.message);
            }
          })
          .catch((res) => {
            this.setState({
              issuccess: false,
              isfinish: true,
            });
          });
      }
    );
  };
  putdatacancel = () => {
    this.state.source.cancel();
  };
  render() {
    const { uploading, fileList, isfinish, issuccess } = this.state;
    const { closehandleputdialog } = this.props;
    const props = {
      beforeUpload: (file) => {
        this.setState({
          fileList: [file],
          filename: file.name,
        });
        return false;
      },
      fileList,
    };
    return (
      <>
        <div className="derivedresults_modaltip">温馨提示</div>
        <div className="derivedresults_modalbody">
          {!isfinish && (
            <div>
              <div>
                <span style={{ color: "red" }}>*</span> 请选择导入文件
              </div>
              <div style={{ marginTop: "5px" }}>
                <Upload {...props}>
                  <div className="uploadmsgandbtn">
                    <div className="uploadmsgandbtn_msg">
                      {this.state.filename}
                    </div>
                    <FolderAddFilled
                      style={{
                        fontSize: "20px",
                        marginLeft: "10px",
                      }}
                    />
                  </div>
                </Upload>
              </div>
            </div>
          )}
          {isfinish && (
            <div className="uploadmsgandbtn_issuccess">
              <span> {!issuccess && `已取消导入 `}</span>
              <span className="filename">{this.state.filename}</span>
              <span>{issuccess && ` 导入完成!`}</span>
            </div>
          )}
        </div>
        <div className="derivedresults_footer">
          {!isfinish && (
            <div>
              {!uploading && (
                <Button
                  onClick={this.handleUpload}
                  disabled={fileList.length === 0}
                  style={{
                    marginRight: "20px",
                    background:
                      fileList.length === 0 ? "#ccc" : "rgba(62, 202, 203, 1)",
                    borderColor:
                      fileList.length === 0 ? "#ccc" : "rgba(62, 202, 203, 1)",
                  }}
                >
                  上传
                </Button>
              )}
              {uploading && (
                <Button
                  onClick={this.putdatacancel}
                  style={{
                    marginRight: "20px",
                    background: "#fff",
                    borderColor: "rgba(62, 202, 203, 1)",
                    color: "rgba(62, 202, 203, 1)",
                  }}
                >
                  取消上传
                </Button>
              )}
            </div>
          )}
          {isfinish && (
            <div>
              <Button
                style={{
                  marginRight: "20px",
                }}
                onClick={() => {
                  this.setState({
                    issuccess: false,
                    isfinish: false,
                    uploading: false,
                    handleresdialog: false,
                    filename: "请选择",
                    fileList: [],
                  });
                  closehandleputdialog(false);
                }}
              >
                确认
              </Button>
            </div>
          )}
        </div>
      </>
    );
  }
  componentWillReceiveProps(val) {
    if (!val.handleputdialog) {
      this.setState({
        filename: "请选择",
        fileList: [],
        uploading: false,
        isfinish: false,
      });
    }
  }
}
