// import city from ;
let city = require("../../component/threelinkage/city.json");
let menumsg = function () {
  let one = localStorage.getItem("city").split("-")[0];
  let two = localStorage.getItem("city").split("-")[1];
  let three = localStorage.getItem("city").split("-")[2];
  const provinceData = Object.keys(city);
  const cityDatas = provinceData.map((item, index) => {
    let obj = {};
    obj[item] = Object.keys(Object.values(city)[index]);
    return obj;
  });
  let objss = {};
  provinceData.map((item, index) => {
    Object.assign(objss, Object.values(city)[index]);
  });
  let objs = {};
  cityDatas.map((items) => {
    Object.assign(objs, items);
  });
  let menumsgs = {};
  menumsgs.city = two + "-" + three;
  menumsgs.allcounty = objss[two].join(",");
  if (three === "市本级" && localStorage.getItem("iscity") === "1") {
    return menumsgs;
  } else {
    return {
      city: "",
      allcounty: "",
    };
  }
};

module.exports = {
  menumsgs: menumsg,
};
