import React, { Component } from "react";
import { Route, Redirect, Switch, withRouter } from "react-router-dom";
class RouterView extends Component {
  render() {
    let { list } = this.props;
    let menu = localStorage.getItem("menu")
      ? localStorage.getItem("menu").split(",")
      : [];
    let newList = list.filter((res) => {
      if (
        menu.includes(res.cdoe) ||
        res.name === "Layout" ||
        res.name === "Login"
      ) {
        if (
          localStorage.getItem("ename") !== "ypl" &&
          res.name === "Handeldomin"
        ) {
        } else {
          return res;
        }
      }
    });
    let redArr = list.filter((item) => item.from);
    return (
      <Switch>
        {newList.map((item, index) => {
          let Com = item.component;
          return (
            <Route
              key={index}
              path={item.path}
              render={(pro) => {
                if (item.code && sessionStorage.getItem("token")) {
                  return (
                    <Redirect
                      to={{
                        pathname: "/login",
                      }}
                    />
                  );
                } else if (item.children) {
                  return (
                    <Com
                      {...pro}
                      children={item.children}
                      arr={item.children.filter((item) => !item.from)}
                    />
                  );
                } else {
                  return <Com {...pro} />;
                }
              }}
            />
          );
        })}
        {redArr.map((item, index) => {
          return <Redirect key={index} from={item.from} to={item.to} />;
        })}
      </Switch>
    );
  }
  componentDidMount() {
    this.props.history.listen((inner) => {
      if (
        inner.pathname === "/" ||
        (!sessionStorage.getItem("token") && inner.pathname !== "/login")
      ) {
        localStorage.setItem("mobiles", "");
        localStorage.setItem("field", "");
        localStorage.setItem("menu", "");
        localStorage.setItem("uid", "");
        sessionStorage.clear();
        this.props.history.push("/login");
        return;
      }
      if (inner.pathname === "/login") {
        localStorage.setItem("mobiles", "");
        localStorage.setItem("field", "");
        localStorage.setItem("menu", "");
        localStorage.setItem("uid", "");
        sessionStorage.clear();
        return;
      }
    });
  }
}
export default withRouter(RouterView);
