import { message } from "antd";
const $ = {
  success: (val) => {
    message.success(val);
  },
  error: (val) => {
    message.error(val);
  },
  debounce: (fn, wait) => {
    var timeout = null;
    return function () {
      if (timeout !== null) clearTimeout(timeout);
      timeout = setTimeout(fn, wait);
    };
  },
};

export default $;
