import React, { Component } from "react";
import { Radio, Checkbox } from "antd";
import "./index.scss";

const CheckboxGroup = Checkbox.Group;
export default class Allcheck extends Component {
  state = {
    checkedListdialog: this.props.defaultfieldmsg,
    plainOptionsdialog: this.props.fieldmsg,
    checkAlldialog: 2,
  };
  //导出结果多选
  onChangedialog = (checkedListdialog) => {
    if (checkedListdialog.length === 0) {
      this.setState({
        checkedListdialog,
        checkAlldialog: 2,
      });
    } else {
      this.setState({
        checkedListdialog,
        checkAlldialog:
          checkedListdialog.length === this.state.plainOptionsdialog.length
            ? 1
            : 3,
      });
    }
    this.props.watchdatachange(checkedListdialog);
  };
  onCheckAllChange = (e) => {
    this.setState({
      checkedListdialog:
        e.target.value === 1
          ? this.state.plainOptionsdialog.map((res) => {
              return res.value;
            })
          : [],
      checkAlldialog: e.target.value,
    });
    this.props.watchdatachange(
      e.target.value === 1
        ? this.state.plainOptionsdialog.map((res) => {
            return res.value;
          })
        : []
    );
  };
  render() {
    return (
      <div className="dialgderive">
        <div className="site-checkbox-all-wrapper">
          <Radio.Group
            onChange={this.onCheckAllChange}
            value={this.state.checkAlldialog}
            className
          >
            <Radio value={1}>全选</Radio>
            <Radio value={2}>全不选</Radio>
          </Radio.Group>
        </div>
        <br />
        <CheckboxGroup
          options={this.state.plainOptionsdialog}
          value={this.state.checkedListdialog}
          onChange={this.onChangedialog}
          className="checkboxgroupwrapper"
        />
      </div>
    );
  }
  componentDidMount() {
    this.onChangedialog(this.state.checkedListdialog);
  }
  componentWillReceiveProps(val) {
    if (val.defaultfieldmsg.length === 0) {
      this.setState({
        checkedListdialog: val.defaultfieldmsg,
        checkAlldialog: 2,
      });
    } else {
      this.setState({
        checkedListdialog: val.defaultfieldmsg,
        checkAlldialog:
          val.defaultfieldmsg.length === this.state.plainOptionsdialog.length
            ? 1
            : 3,
      });
    }
  }
}
