import axios from "axios";
import { Component } from "react";
import $ from "./index";
let base = "/api";
// 请求前拦截
axios.interceptors.request.use(
  (config) => {
    if (
      config.headers.Apiname !== "loginregion" &&
      config.headers.Apiname !== "smsverification" &&
      config.headers.Apiname !== "userloginapi"
    ) {
      let timer = sessionStorage.getItem("handletime");
      if (+new Date() - +timer > 1800000) {
        $.error("登陆超时请重新登陆");
        let str = window.location.href;
        var offset = 0;
        var count = 0;
        var index = -1;
        if (str.length > 0) {
          while ((offset = str.indexOf("/")) != -1) {
            if (count == 3) {
              break;
            }
            count++;
            str = str.substring(offset + 1);
            index += offset + 1;
          }
        }

        sessionStorage.clear();
        localStorage.setItem("mobiles", "");
        localStorage.setItem("field", "");
        localStorage.setItem("menu", "");
        localStorage.setItem("uid", "");
        setTimeout(() => {
          window.location.href = window.location.href.slice(0, index);
        }, 2000);
      } else {
        sessionStorage.setItem("handletime", +new Date());
      }
    }
    return config;
  },
  (err) => {
    console.log("请求超时");
    return Promise.reject(err);
  }
);

// 返回后拦截
axios.interceptors.response.use(
  (data) => {
    return data;
  },
  (err) => {
    // if (err.response.status === 504 || err.response.status === 404) {
    //   console.log("服务器被吃了⊙﹏⊙");
    // } else if (err.response.status === 401) {
    //   console.log("登录信息失效⊙﹏⊙");
    // } else if (err.response.status === 500) {
    //   console.log("服务器开小差了⊙﹏⊙");
    // }
    console.log(err);
    return Promise.reject(err);
  }
);

// @RequestBody请求
const post = (url, params, headers = {}) => {
  return axios({
    method: "post",
    url: `${base}${url}`,
    data: params,
    headers: Object.assign(headers, {
      "Content-Type": "application/json",
      charset: "utf-8",
    }),
  });
};

// @RequsetParam请求
const postRequestParam = (url, params, headers = {}) => {
  return axios({
    method: "post",
    url: `${base}${url}`,
    data: params,
    transformRequest: [
      function (data) {
        let ret = "";
        for (let it in data) {
          ret +=
            encodeURIComponent(it) + "=" + encodeURIComponent(data[it]) + "&";
        }
        return ret;
      },
    ],
    headers: Object.assign(headers, {
      "Content-Type": "application/x-www-form-urlencoded",
    }),
  });
};
const get = (url, params, headers = {}) => {
  return axios({
    method: "get",
    params: params,
    url: `${base}${url}`,
    headers: headers,
  });
};
const multiple = function (requsetArray, callback) {
  axios.all(requsetArray).then(axios.spread(callback));
};

Component.prototype.get = get;
Component.prototype.post = post;
Component.prototype.postRequestParam = postRequestParam;
Component.prototype.multiple = multiple;
